






















import {
  defineComponent, reactive, computed, ref, toRefs, watch,
} from '@vue/composition-api';
import OverText from '@/components/OverText.vue';
import openWindow from '@/utils/open-window';
import i18n from '@/i18n';
import focusWatch from '@/utils/focus-watch';
import { Col, Item } from '../index.vue';
import EditModel from '../../modelInfo/index.vue';
import useCols from './useCols';
import useWidthTransitionEnd from './useWidthTransitionEnd';
import ReviewItem from './Item.vue';


export default defineComponent({
  name: 'meet-home__review--list',
  components: { ReviewItem },
  props: {
    meetingId: {
      type: String,
      required: true,
    },
    colsNum: {
      type: Number as () => 4 | 8 | 16,
      default: 8,
    },
    list: {
      type: Array as () => Item[],
      default: () => [],
    },
    searchValue: {
      type: String,
      default: '',
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
    col: {
      col: Object,
      default: () => ({}),
    },
  },
  setup(props, { refs, emit, root }) {
    const {
      currentColsNum, review, colWidthStyle, itemHeightStyle,
    } = useCols(props.colsNum);
    const colWrapStyle = computed(() => ({
      ...colWidthStyle.value,
      paddingRight: `${props.colsNum === 16 ? 10 : 30}px`,
    }));
    const { overText, handleWidthTransitionend } = useWidthTransitionEnd();


    const toCols = () => {
      emit('to-cols');
    };
    const editItem = async (item: Item) => {
      const sku = await root.$injectDialog({
        is: EditModel,
        props: {
          catalogId: item.bindRelateCatalogId,
          modelInfo: item,
        },
      }, {
        key: (new Date()).valueOf(),
      });
      emit('refresh');
    };

    const toDetail = async (col: Col, item: Item) => {
      const params = {
        columnId: item.meetingColumnId,
        meetingId: props.meetingId,
        itemId: item.id,
      };
      openWindow({ path: '/up-meeting/detail', query: params });
    };

    focusWatch(() => {});

    return {
      toCols,
      currentColsNum,
      review,
      colWrapStyle,
      itemHeightStyle,
      overText,
      handleWidthTransitionend,
      editItem,
      toDetail,
    };
  },
});
