
















































import {
  defineComponent, reactive, computed, ref, toRefs, watch,
} from '@vue/composition-api';
import OverText from '@/components/OverText.vue';
import openWindow from '@/utils/open-window';
import i18n from '@/i18n';
import focusWatch from '@/utils/focus-watch';
import EditModel from '../../modelInfo/index.vue';
import useCols from './useCols';
import useWidthTransitionEnd from './useWidthTransitionEnd';
import { Item, Col } from './index.vue';
import ReviewItem from './Item.vue';


export default defineComponent({
  name: 'meet-review__cols',
  components: { OverText, ReviewItem },
  props: {
    meetingId: {
      type: String,
      required: true,
    },
    colsNum: {
      type: Number as () => 4 | 8 | 16,
      default: 8,
    },
    cols: {
      type: Array as () => Col[],
      default: () => [],
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      swipeIndex: 0,
    });
    const {
      currentColsNum, review, colWidth, colWidthStyle, itemHeightStyle,
    } = useCols(props.colsNum);
    const colWrapStyle = computed(() => ({
      ...colWidthStyle.value,
      paddingRight: `${currentColsNum.value === 16 ? 10 : 30}px`,
    }));
    const { overText, handleWidthTransitionend } = useWidthTransitionEnd();
    watch(currentColsNum, () => {
      state.swipeIndex = 0;
    });
    const containerStyle = computed(() => ({
      transform: `translateX(calc(-${colWidth.value * state.swipeIndex}% + 5px) )`, // 5pxshadow
    }));
    const maxSwipeIndex = computed(() => props.cols.length - currentColsNum.value);
    const swipe = (type: 'left' | 'right') => {
      switch (type) {
        case 'left':
          state.swipeIndex--;
          break;
        case 'right':
          state.swipeIndex++;
          break;
        default:
          break;
      }
      state.swipeIndex = Math.max(0, Math.min(state.swipeIndex, maxSwipeIndex.value));
    };

    const openColList = (colI: number) => {
      // Col,index,
      emit('open-col-list', colI);
    };
    const editItem = async (item: Item) => {
      const sku = await root.$injectDialog({
        is: EditModel,
        props: {
          catalogId: item.bindRelateCatalogId,
          modelInfo: item,
        },
      }, { key: (new Date()).valueOf() });
      emit('refresh');
    };

    const toDetail = async (col: Col, item: Item) => {
      const params = {
        columnId: item.meetingColumnId,
        meetingId: props.meetingId,
        itemId: item.id,
      };
      root.$router.push({ path: '/up-meeting/detail', query: params });
    };

    focusWatch(() => {});
    return {
      ...toRefs(state),
      colWrapStyle,
      itemHeightStyle,
      containerStyle,
      maxSwipeIndex,
      swipe,
      currentColsNum,
      review,
      openColList,
      overText,
      handleWidthTransitionend,
      editItem,
      toDetail,
    };
  },
});
