import {
  reactive, computed, ref, toRefs, getCurrentInstance, onMounted, watch, SetupContext, markRaw,
} from '@vue/composition-api';
import { hDrag } from '@/components/drags/DragList';
import Catalog, { CatalogOptions, AlbumItem } from '@/models/Catalog';
import { validFiles } from '@/utils/validators/valid-file';
import { getFileName } from '_/get-ext';
import i18n from '@/i18n';
import validatorCan from '@/utils/validators/can';
import { updateWorkspaceRolesAndPermissions } from '@/loaders/context';
import AppError from '@/utils/error';
import CreateSku, { submitModelCore } from '../components/CreateSku.vue';
import EditModel, { checkHasModel, submitCore } from '../modelInfo/index.vue';
import { isImage } from '../components/List.vue';
import { fileList2Album } from '../components/Album.vue';
import { selectRenderPicturesCore } from '../components/FileSelect.vue';
import { Item, Col } from './index.vue';
import useProcess from './useProcess';


type DragAreaType = '' | 'add-item-btn'|'out-drag-insert-area'|'drag-item';
export interface DragInfo {
  type:DragAreaType,
  colIndex: number,
  itemIndex: number,
  itemId:string
}

type Catalogs = Catalog[];
type FilesList = (File[])[];
type FileOrAlbums = (File|AlbumItem)[];

export interface IdeationDragData{
  type:string,
  command:string,
  data:Catalogs,
  workspaceId:string,
  activeFolder:CatalogOptions,
  entryAppType:number,
  dataType: '' | 'img'|'model'
}

const ALLOW_IMG_TYPES = ['png', 'jpg', 'jpeg'];


export const filterFileAndAlbum = (files:FileOrAlbums):[File[], AlbumItem[]] => {
  const [_files, _albums]:[File[], AlbumItem[]] = [[], []];
  files.forEach((file) => {
    if (file instanceof File) {
      _files.push(file);
    } else {
      _albums.push(file);
    }
  });
  return [_files, _albums];
};

const useOutDrag = () => {
  const _this:any = getCurrentInstance();
  const state = reactive({
    dragInfo: {
      type: '',
      colIndex: -1,
      itemIndex: -1,
      itemId: '',
    } as DragInfo,
    files: markRaw<FileOrAlbums>([]),
    ideationDragData: markRaw<IdeationDragData>({
      type: '',
      command: '',
      workspaceId: '',
      data: [],
      activeFolder: {} as CatalogOptions,
      entryAppType: 0,
      dataType: '',
    }),
  });

  const {
    process, dragProcessStart, onprocess, uploadDone, cancelUpload,
  } = useProcess({ isOutDrag: true });

  const reset = () => {
    state.dragInfo = {
      type: '',
      colIndex: -1,
      itemIndex: -1,
      itemId: '',
    };
    state.files = [];
  };

  const getParentEl = (el:HTMLElement|null, parentClass:string):HTMLElement|false => {
    if (!el) return false;
    if (el.classList.contains(parentClass)) return el;
    return getParentEl(el.parentElement, parentClass);
  };

  const handleEnter = (el:HTMLElement|null, type:DragAreaType, e:DragEvent) => {
    const targetEl = getParentEl(el, type);
    if (!targetEl || !targetEl?.classList.contains(type)) return false;
    const colIndex = +(targetEl.dataset.colindex || '-1');
    const itemIndex = +(targetEl.dataset.itemindex || '-1');
    const itemId = targetEl.dataset.itemid || '';
    state.dragInfo = {
      type,
      colIndex,
      itemIndex,
      itemId,
    };
    return true;
  };

  const file2promise = async (item:any, path: string):Promise<File & {_webkitRelativePath:string}> => {
    return new Promise((resolve) => {
      item.file((file: File & {_webkitRelativePath:string}) => {
        file._webkitRelativePath = `${path}${file.name}`;
        resolve(file);
      });
    });
  };

  const directory2promise = async (item:any):Promise<any[]> => {
    return new Promise((resolve) => {
      const dirReader = item.createReader();
      dirReader.readEntries((items: any) => {
        resolve(items);
      });
    });
  };

  const getItemPromises = async (_item: any, _path: string = '') => {
    const itemPromises:Promise<File>[] = [];
    const loop = async (item: any, path: string = '') => {
      if (item.isFile) {
        itemPromises.push(file2promise(item, path));
      } else if (item.isDirectory) {
        const items = await directory2promise(item);
        // forawait
        for (let i = 0; i < items.length; i++) {
          await loop(items[i], `${path + item.name}/`);
        }
      }
    };
    await loop(_item, _path);
    return itemPromises;
  };

  // const getFileOrDierctoryItems = async (dragItems:DataTransferItemList) => {
  // ,file2promisedirectory2promise,dragItems
  //   const filesList = [];
  //   // Items
  //   // eslint-disable-next-line no-restricted-syntax
  //   for (const _dragItem of [...dragItems]) {
  //     const dragItem = _dragItem.webkitGetAsEntry();
  //     let fileOrDirectorItem;
  //     if (dragItem.isFile) {
  //       fileOrDirectorItem = await file2promise(dragItem);
  //     } else if (dragItem.isDirectory) {
  //       fileOrDirectorItem = await directory2promise(dragItem);
  //     }
  //     filesList.push(fileOrDirectorItem);
  //   }
  //   return filesList;
  // };

  const isIdeationItems = (files:File[]|Catalogs):files is Catalogs => {
    return (files as Catalogs).every(item => item.relateType !== undefined);
  };

  const allFilesIsImage = (files:File[]|Catalogs) => {
    if (isIdeationItems(files)) {
      return files.every(item => item.relateType === 2 && isImage(item.name, item.relateType));
    }
    return files.every(item => isImage(item.name, 2));
  };

  const isModel = (file:Catalog) => [0, 3].includes(file.relateType!);

  const checkFilesLength = (files:(File|AlbumItem)[]|Catalogs, editItem:Item) => {
    const originImgLength = editItem?.relateInfo?.imgList?.length || 0; // imgList0?
    if ((files.length + originImgLength) > 20) {
      reset();
      throw new Error(i18n.t('virtual-meetup.check.length'));
    }
  };

  const checkFilesType = (files:File[]|Catalogs) => {
    try {
      if (isIdeationItems(files)) {
        // ideation
        // if (files.some(file => file.relateType === null)) {
        //   // 
        //   throw new Error(i18n.t('virtual-meetup.check.type'));
        // }
        // 
        const [modelFiles, otherFiles] = files.reduce(([_modelFiles, _otherFiles], item) => {
          const _files = isModel(item) ? _modelFiles : _otherFiles;
          _files.push(item);
          return [_modelFiles, _otherFiles];
        }, [[], []] as [Catalogs, Catalogs]);
        if (modelFiles.length > 1) {
          throw new Error(i18n.t('virtual-meetup.check.model_limit'));
        }
        if (modelFiles.length > 0 && otherFiles.length > 0) {
          throw new Error(i18n.t('virtual-meetup.check.exclusion_type'));
        }
        const needValidFiles = otherFiles.map(item => {
          const { downloadPath } = item.fileInfo || {};
          if (downloadPath) {
            return getFileName(downloadPath);
          }
          throw new Error(i18n.t('virtual-meetup.check.type'));
        });
        validFiles(needValidFiles, { extensions: ALLOW_IMG_TYPES });
      } else {
        // 
        validFiles(files, { extensions: ALLOW_IMG_TYPES });
      }
    } catch (error) {
      reset();
      if (error.isFileTypeError) {
        throw new Error(i18n.t('virtual-meetup.check.type'));
      }
      throw error;
    }
  };


  const checkIdeationCan = async (workspaceId:string, entryAppType:number) => {
    // /
    if ([11, 26].includes(entryAppType)) return;
    await updateWorkspaceRolesAndPermissions(workspaceId);
    try {
      validatorCan('56', { workspaceId }, false);
    } catch (error) {
      reset();
      throw error;
    }
  };

  const checkIdeationFiles = async (files:Catalogs, editItem:Item, isEdit:boolean, ideationDragData:IdeationDragData) => {
    const { workspaceId, entryAppType } = ideationDragData;
    await checkIdeationCan(workspaceId, entryAppType);
    checkFilesLength(files, editItem);
    checkFilesType(files);
  };

  // Thumbs.db: https://baike.baidu.com/item/Thumbs.db/5099306?fr=aladdin
  const filterLocalFileNames = (file:File) => ['Thumbs.db'].includes(file.name);

  const getDragDataType = (files:Catalogs) => {
    const _isModel = isModel(files[0]);
    const isImg = allFilesIsImage(files);
    // eslint-disable-next-line no-nested-ternary
    return _isModel ? 'model' : isImg ? 'img' : '';
  };

  const setLocalFiles = async (e:DragEvent, editItem:Item) => {
    state.files = [];
    const items = e.dataTransfer?.items;
    if (!items || [...items].some(item => item.kind !== 'file')) {
      reset();
      throw AppError.abort;
    }

    // const filesList = await getFileOrDierctoryItems(items);
    // await
    const itemPromisesList = await Promise.all([...items].map(async _item => {
      const item = _item.webkitGetAsEntry();
      const itemPromises = await getItemPromises(item);
      return itemPromises;
    }));

    const filesList:FilesList = [];
    // eslint-disable-next-line no-restricted-syntax
    for (const itemPromises of itemPromisesList) {
      filesList.push(await Promise.all(itemPromises));
    }
    let flatFiles:(File|AlbumItem)[] = [];
    // eslint-disable-next-line no-restricted-syntax
    for (const files of filesList) {
      checkFilesType(files);
      const isDirectory = files.length > 1;
      if (isDirectory) {
        // 
        const albums:AlbumItem[] = await fileList2Album(files);
        flatFiles = [...flatFiles, ...albums];
      } else {
        // 
        const file = files[0];
        if (filterLocalFileNames(file)) return;
        flatFiles.push(file);
      }
    }
    state.files = flatFiles;
    checkFilesLength(state.files, editItem);
  };

  const setIdeationFiles = async (ideationDragData:IdeationDragData, editItem:Item, isEdit:boolean) => {
    const { data } = ideationDragData;
    await checkIdeationFiles(data, editItem, isEdit, ideationDragData);
    state.ideationDragData = { ...ideationDragData, dataType: getDragDataType(data) };
  };

  const addSku = async (col: Col, outDragOptions:{[any:string]:any}, dragInfo:DragInfo) => {
    const { type, itemIndex } = dragInfo;
    const isInsert = type === 'out-drag-insert-area';
    const sort = isInsert ? itemIndex : col.itemInfos.length;
    const { isIdeationDrop, ideationDragData } = outDragOptions;
    const update = (apiData:Item) => {
      if (apiData && typeof apiData === 'object') {
        // ideationsku
        if (isInsert) {
          col.itemInfos.splice(itemIndex, 0, apiData);
        } else {
          col.itemInfos.push(apiData);
        }
      }
    };
    if (isIdeationDrop) {
      // ws
      const {
        data, dataType,
      } = ideationDragData;
      if (dataType === 'model') {
        const { meeting } = _this;
        // 
        const modelData = await selectRenderPicturesCore({ files: data, meetingCatalogId: meeting.entryId, isModel: true });
        const apiData = await submitModelCore({
          modelData, parentFolderId: meeting.entryId, sort, meetingColumnId: col.id,
        });
        update(apiData);
        return;
      }
    }
    // 
    const apiData = await _this.$injectDialog({
      is: CreateSku,
      props: {
        meetingColumnId: col.id,
        sort,
        meetingId: _this.meetingId,
        outDragOptions,
        processStart: ({ coverImage, temId }:{coverImage:AlbumItem, temId:string}) => {
          _this.isOutDrag = true;
          return dragProcessStart({
            coverImage, temId, dragInfo, col,
          });
        },
        onprocess,
        uploadDone,
      },
    }, {
      key: (new Date()).valueOf(),
    });
    update(apiData);
  };

  const editSku = async (item: Item, col: Col, itemIndex: number, outDragOptions:{[any:string]:any}, dragInfo:DragInfo) => {
    const { isIdeationDrop, ideationDragData } = outDragOptions;
    const update = (apiData:Item) => {
      if (apiData && typeof apiData === 'object') {
        _this.$set(col.itemInfos, itemIndex, apiData);
      }
    };
    const modelInfo = { ...item, sort: itemIndex };
    try {
      if (isIdeationDrop) {
        // ws
        const {
          data, dataType,
        } = ideationDragData;
        if (dataType === 'model') {
          const hasModel = item.relateInfo.oldRelateId;
          if (hasModel) {
            throw new Error(i18n.t('virtual-meetup.check.model_limit'));
          }
          const { meeting } = _this;
          // 
          const modelData = await selectRenderPicturesCore({ files: data, meetingCatalogId: meeting.entryId, isModel: true });
          const apiData = await submitModelCore({
            modelData, parentFolderId: meeting.entryId, sort: itemIndex, meetingColumnId: col.id, editItem: modelInfo,
          });
          update(apiData);
          return;
        }
      }
      // 
      await _this.$injectDialog({
        is: EditModel,
        props: {
          modelInfo,
          outDragOptions,
          meetingId: _this.meetingId,
          processStart: ({ coverImage, temId }:{coverImage:AlbumItem, temId:string}) => {
            _this.isOutDrag = true;
            return dragProcessStart({
              coverImage, temId, col, dragInfo, item,
            });
          },
          onprocess,
          uploadDone,
        },
      }, {
        key: (new Date()).valueOf(),
      });
    } catch (error) {
      reset();
      throw error;
    }
  };

  const handleDrop = async (e:DragEvent) => {
    const isEdit = state.dragInfo.type === 'drag-item';
    const isAdd = ['add-item-btn', 'out-drag-insert-area'].includes(state.dragInfo.type);
    if (!isEdit && !isAdd) return;

    const targetEl = getParentEl(e.target as HTMLElement, 'drag-item');
    if (targetEl && targetEl.querySelector('.uploading')) {
      return;
    }

    const ideationDragDataStr = e.dataTransfer?.getData('ideationDragData');
    const ideationDragData = ideationDragDataStr ? JSON.parse(ideationDragDataStr) : '';
    const isIdeationDrop = !!ideationDragData;

    const { colIndex, itemIndex } = state.dragInfo;
    const col = _this.currentCols[colIndex];
    const editItem = isAdd ? {} : col.itemInfos[itemIndex];

    if (ideationDragData) {
      await setIdeationFiles(ideationDragData, editItem, isEdit);
    } else {
      await setLocalFiles(e, editItem);
    }


    const outDragOptions = {
      isIdeationDrop,
      files: state.files,
      ideationDragData: state.ideationDragData,

    };

    if (isAdd) {
      await addSku(col, outDragOptions, state.dragInfo);
    } else if (isEdit) {
      await editSku(editItem, col, itemIndex, outDragOptions, state.dragInfo);
    }
  };

  const containerEnter = (e: DragEvent) => {
    if (hDrag.isDragging) return;
    const el = e.target as HTMLElement;
    const types:DragAreaType[] = ['add-item-btn', 'out-drag-insert-area', 'drag-item'];
    for (let i = 0, len = types.length; i < len; i++) {
      const type = types[i];
      const isBreak = handleEnter(el, type, e);
      if (isBreak) {
        break;
      } else {
        reset();
      }
    }
  };


  const containerDrop = async (e:DragEvent) => {
    try {
      await handleDrop(e);
    } finally {
      reset();
    }
  };


  return {
    ...toRefs(state),
    containerEnter,
    containerDrop,
    process,
    cancelUpload,
  };
};

export default useOutDrag;
