async function getMeetingInfo(meetingId: string) {
  const res = (await axios.get<{ id: string, entryId: string, name: string, type: 1 | 2 }>('/meeting/meeting/info', { params: { meetingId } })).data;
  return res;
}

function isHandbag(catalog: Catalog): catalog is CatalogModel {
  return catalog.relateType === 3 && !!catalog.fileInfo;
}

const checkHasModel = async (catalog:Catalog) => {
  const modelId = catalog.relateId;
  if (!modelId) return false;
  const ctmList = (await getModelCTMList(modelId, isHandbag(catalog) ? 'HANDBAG' : 'SHOE')).data;
  return !!(ctmList && ctmList.length);
};


export function catalogs2imgList(catalogs: CatalogModel[]) {
  const imgList = catalogs.map((c, idx) => {
    return {
      path: getRelativePathFromUrl(c.contentThumbnail ?? ''),
      url: c.contentThumbnail,
      imgArgument: '""', // 
      imgType: 1, // ;1-2-3603-7204-
      businessType: 1, // ;1- Integer
      sort: idx, //  Integer
      imgDetail: JSON.stringify(c.fileInfo), // ;json String
      fileSize: null, //  Integer
      isCover: idx === 0 ? 1 : 0, // ;1-0- Integer
    };
  });
  return imgList;
}

export function submitCore(o: { imgList?: any[], curtailImg?: string, relateId?: string, relateType?: number }, meetingCatalogId: string) {
  const data = {
    parentFolderId: meetingCatalogId,
    oldRelateId: o.relateId,
    relateType: o.relateType,
    curtailImg: getRelativePathFromUrl(o.curtailImg ?? ''),
    imgList: o.imgList,
  };
  return data;
}

export async function selectRenderPicturesCore({
  files, meetingCatalogId, callback, isModel,
}:{files: CatalogModel[], meetingCatalogId: string, callback?: Function, isModel:boolean}) {
  if (isModel) {
    const hasModel = await checkHasModel(files[0]);
    if (!hasModel) {
      throw Error(i18n.t('virtual-meetup.public.no_ctm_to_continue'));
    }
  }
  const imgList: AlbumItem[] = [{
    path: getRelativePathFromUrl(files[0].fileInfo.thumbnail ?? ''),
    imgArgument: null,
    imgType: AlbumType.IMG_CURRENT_MODEL,
    businessType: 1,
    sort: 0,
    imgDetail: undefined,
    fileSize: null,
    isCover: 1,
  }];
  if (callback) {
    await callback(imgList);
  }
  return submitCore({
    imgList,
    curtailImg: files[0].fileInfo.thumbnail,
    relateId: files[0].relateId,
    relateType: files[0].relateType,
  }, meetingCatalogId);
}


export default defineComponent({
  dialogify: {
    props: {
      title: i18next.t('virtual-meetup.dialog.addWorkspace'),
      flex: 'column',
      top: 'auto',
      bottom: 'auto',
      width: '580px',
      closeOnClickModal: false,
      // compact: true,
      size: 'medium',
    },
  },
  props: {
    // fileType: {
    //   // 0 -  ; 3 - 
    //   type: Number as () => 0 | 3 | 2,
    //   required: true,
    // },
    name: {
      type: String,
    },
    meetingId: {
      type: String,
    },
    outDragOptions: {
      type: Object as PropType<{
        ideationDragData: IdeationDragData
      }>,
    },
  },
  render() {
    const s = this;
    return (
      <section>
        <el-form size="small" label-position="top">
          <el-form-item label={i18next.t('virtual-meetup.dialog.selectWS')}>
            <el-select vModel={this.selectedWorkspace} style="width: 100%" value-key="id" onchange={
              (val: CatalogOptions) => {
                this.activeFolder = val;
              }
            }>
              {
                this.catalogOptions && this.catalogOptions.length
                  ? this.catalogOptions.map(c => {
                    return <el-option value={c} label={c.workspaceName}>{c.workspaceName}</el-option>;
                  }) : null
              }
            </el-select>
          </el-form-item>
          <el-form-item>
            <slot slot="label">
              <div class="flex-space-between" style="width: 532px;">
                <div>{i18next.t('virtual-meetup.dialog.selectAsset')}</div>
                <div class="type-radio flex-end">
                  <el-radio vModel={this.searchParams.relateType} label={0}>{i18next.t('virtual-meetup.dialog.modelOption')}</el-radio>
                  <el-radio vModel={this.searchParams.relateType} label={2}>{i18next.t('virtual-meetup.dialog.photoOption')}</el-radio>
                </div>
              </div>
            </slot>
            {
              this.activeFolder
                ? <CatalogSelector
                  style={{
                    height: '500px',
                  }}
                  id={this.activeFolder.id}
                  entry-app-type={this.activeFolder.entryAppType}
                  workspace-name={this.selectedWorkspace?.workspaceName}
                  workspace-id={this.activeFolder.workspaceId}
                  search-params={this.searchParams}
                  workspace-business-type={this.activeFolder.workspaceBusinessType}
                  workspace-type={this.activeFolder.workspaceType}
                  selectedCatalog={this.selectedCatalog}
                  onselect-catalog={
                    (catalog: CatalogModel) => s.addCatalog(catalog)
                  }
                  onenterFolder={
                    (catalog: CatalogModel) => {
                      this.activeFolder = catalog;
                    }
                  }
                /> : <div style="height: 500px; position: relative">
                  {
                    // <error error={{
                    //   code: 404,
                    //   message: '',
                    // }} style="font-size: 14px" img={this.isSearch ? require('@/assets/img/error/no-result.png') : undefined} />
                  }
                </div>
            }
          </el-form-item>
        </el-form>
        {
          this.searchParams.relateType === 0
            ? <div class="form-button">
              <el-button round
                disabled={this.selectedCatalog.length === 0}
                onclick={
                  () => {
                    // console.log(this.selectedCatalog);
                    // s.submit([], getRelativePathFromUrl(this.selectedCatalog[0].fileInfo.thumbnail ?? ''), this.selectedCatalog[0].id);
                    s.$act(s.selectRenderPictures, [false]);
                  }
                }>{i18next.t('virtual-meetup.dialog.complete')}</el-button>
              <el-button round type="primary"
                disabled={this.selectedCatalog.length === 0}
                onclick={
                  () => {
                    s.$act(s.selectRenderPictures, [true]);
                  }
                }>{i18next.t('virtual-meetup.dialog.continue')}</el-button>
            </div> : <div class="form-button">
              <el-button round onclick={
                () => { this.$emit('cancel'); }
              }>{i18next.t('virtual-meetup.dialog.cancel')}</el-button>
              <el-button round type="primary"
                disabled={this.selectedCatalog.length === 0}
                onclick={
                  () => s.$act(s.addPictures)
                }>{i18next.t('virtual-meetup.dialog.complete')}({`${this.selectedCatalog.length}/20`})</el-button>
            </div>
        }
      </section>
    );
  },
  setup(props, { root, emit }) {
    const paths = ref<CatalogModel[]>([]);
    const myDesign = 11;
    type CatalogOptionsWithWSName = CatalogOptions & { workspaceName: string };
    const searchParams = reactive({
      relateType: 0,
    });
    const selectedWorkspace = ref<CatalogOptionsWithWSName>();
    const catalogOptions = ref<CatalogOptionsWithWSName[]>([]);
    const activeFolder = ref<CatalogOptions>();
    const meetingCatalogId = ref<string>();
    const selectedCatalog = ref<CatalogModel[]>([]);
    const handleOutDrag = async () => {
      if (props.outDragOptions) {
        const {
          workspaceId, data, dataType, activeFolder: _activeFolder,
        } = props.outDragOptions.ideationDragData;
        const workspace = catalogOptions.value.find(item => item.workspaceId === workspaceId);
        if (!workspace || !dataType) return;
        selectedWorkspace.value = workspace;
        searchParams.relateType = dataType === 'model' ? 0 : 2;
        activeFolder.value = _activeFolder;
        await root.$nextTick();// watchselectedCatalog
        selectedCatalog.value = data as CatalogModel[];
      }
    };
    onMounted(async () => {
      await refreshWorkspaceWithoutMembers();
      const workspaces = WorkspaceOrm.query().orderBy('sort').get();
      const myMaterialCatalogId = await getCatalogId({ entryAppType: myDesign });

      // /meeting/meeting/info
      const catalogs = await Promise.all(workspaces.map(async (w: Workspace) => {
        const res = await getCatalogIdFromWorkspaceId(w.groupAppId);
        return res;
      }));
      for (let i = 0; i < workspaces.length; i++) {
        catalogOptions.value.push({
          id: catalogs[i],
          workspaceId: workspaces[i].groupAppId,
          entryAppType: 13,
          workspaceName: workspaces[i].name,
          workspaceBusinessType: workspaces[i].businessType,
          workspaceType: workspaces[i].type,
        });
      }
      // 
      catalogOptions.value.unshift({
        entryAppType: myDesign,
        workspaceName: appType2Name[myDesign],
        id: myMaterialCatalogId,
        workspaceBusinessType: 0,
      });
      // meetingId, 

      if (props.meetingId) {
        const { entryId, type } = await getMeetingInfo(props.meetingId);
        meetingCatalogId.value = entryId;
        if (type === 1) {
          catalogOptions.value.push({
            entryAppType: 26,
            workspaceName: '',
            id: entryId,
            workspaceBusinessType: 0,
          });
        }
      }

      handleOutDrag();
    });

    watch(selectedWorkspace, async (val) => {
      if (val) {
        const { workspaceId } = val;
        if (workspaceId) {
          await updateWorkspaceRolesAndPermissions(workspaceId);
        }
      }
    });

    function addCatalog(catalog: CatalogModel) {
      if (searchParams.relateType === 2) {
        const idx = selectedCatalog.value.findIndex(s => s.id === catalog.id);
        if (idx !== -1) {
          selectedCatalog.value.splice(idx, 1);
        } else {
          selectedCatalog.value.push(catalog);
        }
      } else {
        selectedCatalog.value = [];
        selectedCatalog.value.push(catalog);
      }
    }
    // catalog
    watch(() => ({
      searchParams: { relateType: searchParams.relateType },
      ...activeFolder,
    }), () => {
      selectedCatalog.value = [];
    });

    function ableToSubmit() {
      if (selectedWorkspace.value) {
        console.log(selectedWorkspace.value);
        const { entryAppType, workspaceId } = selectedWorkspace.value;
        if (entryAppType === 13 && workspaceId) {
          can(56, { workspaceId }, false);
        }
      }
      return true;
    }

    function submit(o: { imgList?: any[], curtailImg?: string, relateId?: string, relateType?: number }) {
      const data = submitCore(o, meetingCatalogId.value ?? '');
      emit('confirm', data);
    }

    async function selectRenderPictures(withRenderPicture: boolean, files: CatalogModel[] = selectedCatalog.value as CatalogModel[]) {
      if (!ableToSubmit()) return;
      const callback = async (imgList: AlbumItem[]) => {
        if (withRenderPicture) {
          const res = await root.$injectDialog({
            is: RenderPicture,
            props: {
              id: files[0].id,
              workspaceId: files[0].workspaceId,
              relateType: searchParams.relateType,
              maxLength: 19,
            },
          }) as AlbumItem[];
          // console.log(res);
          imgList.push(...res);
          console.log(imgList);
          // res.unshift({
          //   path: getRelativePathFromUrl(files[0].fileInfo.thumbnail ?? ''),
          //   imgArgument: 'null',
          //   imgType: 4,
          //   businessType: 1,
          //   sort: res.length,
          //   imgDetail: JSON.stringify(files[0].fileInfo),
          //   fileSize: null,
          //   isCover: 0,
          // });
        }
      };
      const data = await selectRenderPicturesCore({
        files: selectedCatalog.value, meetingCatalogId: meetingCatalogId.value!, callback, isModel: searchParams.relateType === 0,
      });
      emit('confirm', data);
    }

    function addPictures() {
      if (!ableToSubmit()) return;
      const imgList = catalogs2imgList(selectedCatalog.value as CatalogModel[]);
      submit({
        imgList,
        curtailImg: selectedCatalog.value[0].contentThumbnail,
        relateType: 2,
      });
    }

    // const selectedWorkspace = computed(() => {
    //   return workspaceList.value.find(w => {
    //     return w.groupAppId === workspaceId.value;
    //   });
    // });
    // const p = computed(async () => {
    //   a.value;
    //   b.value;

    //   await api();
    //   await api2();

    // });
    // watch()
    return {
      selectedWorkspace,
      searchParams,
      selectedCatalog,
      addCatalog,
      catalogOptions,
      activeFolder,
      selectRenderPictures,
      submit,
      addPictures,
    };
  },
});
