
































import {
  defineComponent, reactive, computed, ref, toRefs, watch, onMounted,
} from '@vue/composition-api';
import axios from '@/utils/axios';
import { Col, Item } from '../index.vue';
import api from '../api';
import Cols from './Cols.vue';
import List from './List.vue';
import SearchEmpty from './SearchEmpty.vue';

type LikeObj = {[any:string]:any};

const fetchLikeObj = async (params:{relateIds:string[]}) => {
  if (!params.relateIds?.length) return {};
  const likeObj:LikeObj = (await axios({
    url: '/meeting/userLoveStatistics/statisticsAndUserLoveStatusList',
    method: 'post',
    data: { ...params, relateType: 0 },
  })).data;
  return likeObj;
};

const getAddLikeList = (list:Item[], likeObj:LikeObj) => {
  return list.map(item => ({
    ...item,
    like: {
      userLoveCount: likeObj[item.id]?.userLoveCount ?? 0,
      userLoveStatus: likeObj[item.id]?.userLoveStatus ?? false,
    },
  }));
};

const getAddLikeCols = (cols:Col[], likeObj:LikeObj) => {
  return cols.map(col => ({ ...col, itemInfos: getAddLikeList(col.itemInfos, likeObj) }));
};

export default defineComponent({
  name: 'meet-home__review',
  components: {
    Cols, List, SearchEmpty,
  },
  props: {
    meetingId: {
      type: String,
      required: true,
    },
    showCols: {
      type: Array as () => Col[],
      default: () => [],
    },
    colsNum: {
      type: Number as () => 4 | 8 | 16,
      default: 8,
    },
    col: {
      type: Object,
      default: () => ({}),
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
    searchValue: {
      type: String,
      default: '',
    },
    list: {
      type: Array as () => Item[],
      default: () => [],
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      currentShowCols: props.showCols,
      currentList: props.list,
    });

    const showList = computed(() => {
      return Object.keys(props.col).length || props.isSearch;
    });

    watch(() => [props.list, props.showCols], async () => {
      // fetchLikeObj,
      const relateIds = props.showCols.map(col => col.itemInfos).flat().map(item => item.id);
      const likeObj = await fetchLikeObj({ relateIds });
      state.currentList = getAddLikeList(props.list, likeObj);
      state.currentShowCols = getAddLikeCols(props.showCols, likeObj);
    }, { immediate: true });

    const openColList = (colI: number) => {
      emit('open-col-list', props.showCols[colI]);
    };

    const toCols = () => {
      emit('to-cols');
    };

    const refresh = () => {
      emit('refresh');
    };

    return {
      ...toRefs(state),
      showList,
      openColList,
      toCols,
      refresh,
    };
  },
});
