/* eslint-disable import/prefer-default-export */
import {
  reactive, computed, ref, toRefs, getCurrentInstance, onMounted, watch, SetupContext,
} from '@vue/composition-api';
import { Col } from './index.vue';
import api from './api';

const useListDrag = () => {
  const _this:any = getCurrentInstance();
  const state = reactive({
    originListStartIndex: -1, // 
    listStartIndex: -1,
    listEnterIndex: -1,
    listDraging: false,
  });
  const swap = (list:any[], start:number, end:number) => {
    if (start === end) return;
    const startItem = list.splice(start, 1)[0];
    list.splice(end, 0, startItem);
    state.listStartIndex = end;
  };
  const listDragReset = () => {
    state.originListStartIndex = -1;
    state.listStartIndex = -1;
    state.listEnterIndex = -1;
    state.listDraging = false;
  };
  const listDragStart = ({ e, i, col }:{e:DragEvent, i:number, col:Col}) => {
    if (col.isInput) {
      e.preventDefault();
      return;
    }
    state.originListStartIndex = i;
    state.listStartIndex = i;
    state.listDraging = true;
  };

  const listDragEnter = ({ i, list }:{ i:number, list:any[]}) => {
    const { listStartIndex, listEnterIndex } = state;
    if (state.listEnterIndex === i) return;
    state.listEnterIndex = i;
    // 
    swap(list, listStartIndex, i);
  };
  const listDragEnd = async ({ col }:{col:Col}) => {
    if (state.listEnterIndex === -1) {
      listDragReset();
      return;
    }

    // dragenddragstart_thisvm(endvmenter)
    try {
      await api.moveCol({
        sorts: (_this.currentCols as Col[]).map((item, i) => ({ id: item.id, sort: i })),
      });
    } catch (error) {
      swap(_this.currentCols, state.listEnterIndex, state.originListStartIndex);
      // 
      throw (error);
    } finally {
      listDragReset();
    }
  };
  return {
    ...toRefs(state),
    listDragStart,
    listDragEnter,
    listDragEnd,
  };
};

export default useListDrag;
