import {
  reactive, computed, ref, toRefs, getCurrentInstance, onMounted, watch, SetupContext,
} from '@vue/composition-api';
import selectFiles from '@/utils/select-files';
import uploadToOSS from '@/utils/oss/uploadToOSS';
import validFile from '@/utils/validators/valid-file';
import axios from '@/utils/axios';
import { Meeting } from '../typings';

const fetchMeeting = async (params:{meetingId:string}) => {
  const res:Meeting = (await axios({
    url: '/meeting/meeting/info',
    params,
  })).data || {};
  return res;
};

const operateMeeting = async (params: {id:string, backgroundImage:string}) => {
  const res = (await axios({
    url: '/meeting/meeting/update',
    method: 'post',
    data: params,
  })).data;
  return res;
};

const state = reactive({
  bgLoading: false,
});

const useBg = (meeting:Meeting) => {
  const bgStyle = computed(() => ({
    backgroundImage: `url(${meeting.backgroundImage})`,
  }));
  const updateBg = async ({ backgroundImage }:{backgroundImage:string}) => {
    try {
      state.bgLoading = true;
      await operateMeeting({ id: meeting.id, backgroundImage });
      const newMeeting = await fetchMeeting({ meetingId: meeting.id });
      meeting.backgroundImage = newMeeting.backgroundImage;
    } finally {
      state.bgLoading = false;
    }
  };
  const cancelBg = async () => {
    await updateBg({ backgroundImage: '' });
  };
  const setBg = async () => {
    const allowExt = ['png', 'jpeg', 'jpg'];
    const [file] = await selectFiles(allowExt, 5000);
    await validFile(file, { extensions: allowExt, sizeLimit: 20 * 1024 * 1024 });
    try {
      state.bgLoading = true;
    } finally {
      const imgKey = await uploadToOSS(file, 'MEETING');
      await updateBg({ backgroundImage: imgKey });
      state.bgLoading = false;
    }
  };

  return {
    ...toRefs(state),
    bgStyle,
    setBg,
    cancelBg,
  };
};

export default useBg;

