
import {
  defineComponent, onMounted, ref, PropType, computed,
} from '@vue/composition-api';
import { i18n } from '@/i18n';
import Album from '@/views/up-meeting/components/Album.vue';
import axios from '@/utils/axios';
// import Attachment from '../../dialogComponents/Attachment.vue';
import AppError from '@/utils/error';
import { getFileRelateIdByCatalogId } from '@/api/catalogue';
import { AlbumItem } from '@/models/Catalog';
import Attachment, { binaryFileInfoList2FileList, getBinaryFileList } from '../dialogComponents/Attachment.vue';
import { IdeationDragData, filterFileAndAlbum } from '../home/useOutDrag';
import { catalogs2imgList } from '../components/FileSelect.vue';
import { getModelCTMList } from '../utils/utils';
import { Item } from '../home/index.vue';
import shoeInfoForm from './components/shoeInfo.vue';
import handbagInfoForm from './components/handbagInfo.vue';

type Obj = { [any: string]: any };

export const checkHasModel = async (editItem: Item) => {
  const modelId = editItem?.bindRelateId;// skucatalogId
  if (!modelId) return false; // sku
  const relateType = editItem?.relateInfo?.relateType;
  const ctmList = (await getModelCTMList(modelId, relateType === 3 ? 'HANDBAG' : 'SHOE')).data;
  return !!(ctmList && ctmList.length);
};
/**
 * 04
 */
const getTags = async (shareId: string, type: 0 | 4) => {
  return axios.get<{ tag: string, id: string }[]>('/bff-center/showCommon/getTags', {
    params: {
      shareId,
      type,
    },
  });
};

export const getEditInfo = async (modelInfo: Obj) => {
  const model = JSON.parse(JSON.stringify(modelInfo.relateInfo));
  let binaryFileInfoList: any[] = [];
  let imgList: AlbumItem[] = [];
  const form = JSON.parse(JSON.stringify(model!));
  form.price = { value: form.price, unit: model!.relateType === 0 ? form.monetaryUnit : form.priceUnit };
  // 
  const unPublishedImgList = JSON.parse(JSON.stringify(model!.imgList ?? []));
  const unPublishedBinaryFileInfoList = JSON.parse(JSON.stringify(model!.binaryFileInfoList ?? []));
  let publishedImgList = null;
  let publishedBinaryFileInfoList = null;

  if (modelInfo.bindRelateCatalogId) {
    publishedImgList = (await axios.get('/show/album/imgs', { params: { relateId: modelInfo.bindRelateCatalogId } })).data;
    const filesParentId = (await axios.get('/catalogue/yunpanCatalog/getSystemAppChildCountByParentId', {
      params: {
        parentId: modelInfo.bindRelateCatalogId,
        appType: 11002,
      },
    })).data;

    // IDitem
    publishedBinaryFileInfoList = (await axios.get('bff-center/cloud-disk/list/getChildCatalogByPage', {
      params: {
        catalogId: filesParentId,
        cursor: 1,
        limit: 20,
      },
    })).data.records;
  }

  if (!Object.prototype.hasOwnProperty.call(model, 'isPublished')) {
    // 
    if (modelInfo.bindRelateCatalogId) {
      imgList = [...publishedImgList, ...unPublishedImgList];
      binaryFileInfoList = [...publishedBinaryFileInfoList, ...unPublishedBinaryFileInfoList];
    } else {
      // 
      imgList = unPublishedImgList;
      binaryFileInfoList = unPublishedBinaryFileInfoList;
    }
  } else if (model!.isPublished) {
    // 
    imgList = publishedImgList;
    binaryFileInfoList = publishedBinaryFileInfoList;

    const relateId = (await getFileRelateIdByCatalogId({ relateType: modelInfo.bindRelateType, catalogId: modelInfo.bindRelateCatalogId })).data;
    form.tags = (await getTags(relateId, model.relateType)
    ).data.map((t: { tag: string } | string) => (typeof t === 'string' ? t : t.tag));
  } else {
    // 
    imgList = unPublishedImgList;
    binaryFileInfoList = unPublishedBinaryFileInfoList;
  }
  return {
    binaryFileInfoList, imgList, form,
  };
};

export const getEditRelateInfo = async ({
  binaryFileInfoList, form, modelInfo, imgList,
}: { binaryFileInfoList: any[], form: Obj, modelInfo: Obj, imgList: AlbumItem[], }) => {
  const model = JSON.parse(JSON.stringify(modelInfo.relateInfo));
  // const fileList = binaryFileInfoList2FileList(binaryFileInfoList);
  // 
  const formBinaryFileInfoList = await getBinaryFileList(binaryFileInfoList);
  // 
  const formData: { [key: string]: any } = {
    ...model,
    ...form,
    price: form.price.value,
    // monetaryUnit: form.value.price.unit,
  };

  // set
  if (modelInfo.relateInfo.relateType === 0) {
    formData.monetaryUnit = form.price.unit;
  } else {
    formData.priceUnit = form.price.unit;
  }

  formData.imgList = imgList;
  formData.binaryFileInfoList = formBinaryFileInfoList;
  const relateInfo = {
    ...model,
    ...formData,
    catalogId: modelInfo.bindRelateCatalogId,
    isPublished: false,
  };
  return relateInfo;
};


export const submitCore = async ({
  binaryFileInfoList, form, modelInfo, imgList, coverImageItem,
}: { binaryFileInfoList?: any[], form?: Obj, modelInfo: Obj, imgList?: AlbumItem[], coverImageItem: AlbumItem }) => {
  if (!binaryFileInfoList || !imgList || !form) {
    const { binaryFileInfoList: _binaryFileInfoList, form: _form, imgList: _imgList } = (await getEditInfo(modelInfo));
    binaryFileInfoList = _binaryFileInfoList;
    form = _form;
    imgList = _imgList;
  }

  const relateInfo = await getEditRelateInfo({
    binaryFileInfoList,
    form: form!,
    modelInfo,
    imgList,
  });

  const params = {
    ...modelInfo,
    coverImage: coverImageItem.path,
    relateInfo,
  };

  const apiData = (await axios({
    url: '/meeting/column/item/update',
    method: 'POST',
    data: params,
  })).data;

  return apiData;
};

const ModelInfo = defineComponent({
  name: 'ModelInfo',
  components: {
    shoeInfoForm,
    handbagInfoForm,
  },
  props: {
    catalogId: {
      type: String,
      // required: true,
    },
    modelInfo: {
      type: Object,
      required: true,
    },
    outDragOptions: {
      type: Object as PropType<{
        isIdeationDrop: boolean,
        files: File[],
        ideationDragData: IdeationDragData,
        onprocess({ uploaded, total }: { uploaded: number, total: number }): void
      }>,
    },
    meetingId: {
      type: String,
    },
    processStart: {
      type: Function as PropType<({ coverImage, temId }: { coverImage: AlbumItem, temId: string }) => void>,
      required: true,
    },
    onprocess: {
      type: Function as PropType<({ uploaded, total, temId }: { uploaded: number, total: number, temId: string }) => void>,
      required: true,
    },
    uploadDone: {
      type: Function as PropType<(item: { [any: string]: any }, temId: string) => void>,
      required: true,
    },
  },
  dialogify: {
    props: {
      title: i18n.t('up-meeting.detail.edit'),
      noPadding: true,
      height: '700px',
      width: '587px',
      flex: 'column',
    },

  },
  setup(props, { root, emit }) {
    const attachment = ref<InstanceType<typeof Attachment>>(null);
    const modelForm = ref<InstanceType<typeof shoeInfoForm> | InstanceType<typeof handbagInfoForm>>(null);
    const albumRef = ref<InstanceType<typeof Album>>(null);
    const model = ref<{ [key: string]: any; } | null>(null);
    model.value = JSON.parse(JSON.stringify(props.modelInfo.relateInfo));

    const form = ref<{ [key: string]: any }>(null);

    const imgList = ref<AlbumItem[]>([]);
    const binaryFileInfoList = ref<any[]>([]);

    const hasRelateModel = ref(false);

    const submit = async () => {
      // 
      await modelForm.value.validate();
      if (imgList.value.length === 0) {
        throw new Error(i18n.t('virtual-meetup.check.img_empty'));
      }
      // 
      // await validateForm(modelForm.value as ElForm);
      const temId = props.modelInfo.id;
      const coverImageItem = imgList.value.find(i => i.isCover === 1) || imgList.value[0];
      coverImageItem.isCover = 1;
      const abortController = props.processStart({ coverImage: coverImageItem, temId });

      emit('confirm');
      await albumRef.value.upload(({ uploaded, total }) => {
        props.onprocess({ uploaded, total, temId });
      }, abortController);

      const apiData = await submitCore({
        // binaryFileInfoList: binaryFileInfoList.value,
        binaryFileInfoList: attachment.value.fileList,
        form: form.value,
        modelInfo: props.modelInfo,
        imgList: imgList.value,
        coverImageItem,
      });

      props.uploadDone(apiData, temId);
    };


    const handleOutDrag = async () => {
      if (props.outDragOptions) {
        if (imgList.value.length >= 20) {
          emit('cancel', new AppError(i18n.t('virtual-meetup.check.length')));
          return;
        }
        const { isIdeationDrop, files, ideationDragData } = props.outDragOptions;
        if (isIdeationDrop) {
          // ws
          const {
            data, dataType,
          } = props.outDragOptions.ideationDragData;
          if (dataType === 'img') {
            // 
            const { album } = albumRef.value;
            const _imgList = catalogs2imgList(data).map(item => ({ ...item, isCover: 0 }));
            album.push(..._imgList);
          } else {
            // 
          }
        } else {
          // 
          const { files2albums, album } = albumRef.value;
          const [_files, albums] = filterFileAndAlbum(files);
          // album
          album.push(...albums);
          // imgList.value = [...imgList.value, ...albums];//AlbumJSON.Parse,symbol
          // 
          files2albums(_files);
        }
      }
    };

    onMounted(async () => {
      const {
        binaryFileInfoList: _binaryFileInfoList,
        imgList: _imgList,
        form: _form,
      } = await getEditInfo(props.modelInfo);

      binaryFileInfoList.value = _binaryFileInfoList;
      imgList.value = _imgList;
      // // 
      // const formSet = JSON.parse(JSON.stringify(model.value!));
      // formSet.price = { value: formSet.price, unit: model.value!.relateType === 0 ? formSet.monetaryUnit : formSet.priceUnit };
      form.value = _form;
      hasRelateModel.value = await checkHasModel(props.modelInfo);
      await handleOutDrag();
    });

    return {
      submit,
      attachment,
      modelForm,
      albumRef,
      model,
      form,
      imgList,
      binaryFileInfoList,
      hasRelateModel,
    };
  },

  render() {
    const { submit } = this;

    const modelInfoForm = this.modelInfo.relateInfo.relateType === 0 ? shoeInfoForm : handbagInfoForm;

    return <div class="material-info flex-column">
      <div class="flex-1 scrollable-y">

        {this.form
          ? [
            <Album types={['jpeg', 'jpg', 'png']} ref="albumRef"
              vModel={this.imgList}
              relateType={this.modelInfo.bindRelateType}
              catalogId={this.modelInfo.bindRelateCatalogId}
              hasRelateModel={this.hasRelateModel}></Album>,
            <el-divider></el-divider>,
            <modelInfoForm ref="modelForm" form={this.form}></modelInfoForm>,
          ] : null
        }
        <el-divider></el-divider>
        <Attachment ref="attachment"
          binaryFileInfoList={this.binaryFileInfoList}
          catalogId={this.modelInfo.bindRelateCatalogId || ''}>
        </Attachment>
      </div>
      <div style="display: flex;flex-direction: row-reverse;padding-top:10px">
        <el-button round style="margin-left: 10px" type="primary" onclick={submit}>{i18n.t('up-meeting.detail.save')}</el-button>
        <el-button round onclick={() => this.$emit('cancel')}>{i18n.t('up-meeting.detail.cancel')}</el-button>
      </div>
    </div>;
  },
});

export default ModelInfo;
