






































































import {
  defineComponent, reactive, computed, ref, toRefs, onMounted, getCurrentInstance, watch,
} from '@vue/composition-api';
import useResize from '@/utils/composables/use-resize';
import { Meeting } from '../typings';
import Header, { OperateType } from './Header.vue';
import Edit from './Edit.vue';
import Review from './review/index.vue';
import Empty from './Empty.vue';
import api from './api';
import { Process } from './useProcess';
import useBg from './useBg';

export type Obj = { [any: string]: any };

export type Item = Obj;

export type Col = {
  id: string,
  name: string,
  temName: string,
  itemInfos: Item[],
  isInput?: boolean
};

export default defineComponent({
  name: 'meet-home',
  components: {
    Header, Edit, Review, Empty,
  },
  props: {
    meeting: {
      type: Object as unknown as () => Meeting,
      required: true,
    },
    operateType: {
      type: String,
      default: 'review',
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      meetingId: '',
      currentMeeting: {} as Meeting,
      currentOperateType: props.operateType,
      allCols: [],
      showCols: [] as Col[],
      editCols: [] as Col[],
      colsNum: 8,
      currentCol: {} as Col,
      searchValue: '',
      isSearch: false,
      loading: true,
      height: 0,
      list: [] as Item[],
      uploadProcess: {},
    });
    const { bgStyle, bgLoading } = useBg(props.meeting);
    const header = ref();
    const setHeight = () => {
      if (!header.value) return;
      const winHei = document.documentElement.clientHeight;
      const headerBottomHei = header.value.$el.getBoundingClientRect().bottom;
      const MARGIN = 40;
      state.height = winHei - headerBottomHei - MARGIN;
    };

    useResize(setHeight);


    watch(() => props.operateType, () => {
      state.currentOperateType = props.operateType;
    }, { immediate: true });

    watch(() => props.meeting, (meeting) => {
      state.currentMeeting = meeting;
    }, { immediate: true });

    const isEdit = computed(() => state.currentOperateType === 'edit');
    const showBackBtn = computed(() => (state.showCols.length && Object.keys(state.currentCol).length) || state.isSearch);
    const mainStyle = computed(() => ({
      height: state.height ? `${state.height}px` : 'auto',
      // marginTop: showBackBtn.value ? '0' : undefined,
    }));
    const title = computed(() => state.searchValue || state.currentCol?.name || '');


    const fetchList = async () => {
      try {
        state.loading = true;
        if (!state.isSearch) {
          // 
          state.list = state.currentCol.itemInfos;
        } else {
          // 
          state.list = await api.fetchSearch({ condition: state.searchValue, catalogId: state.currentMeeting.entryId });
        }
      } finally {
        state.loading = false;
      }
    };

    const load = async () => {
      try {
        state.loading = true;
        state.meetingId = state.currentMeeting.id;
        state.allCols = (await api.fetchReviewCols({ meetingId: state.meetingId, meetingColumnSize: 10000, meetingColumnPage: 1 }))?.records || [];
      } finally {
        state.loading = false;
      }
    };

    const regetMeeting = async () => {
      try {
        state.loading = true;
        state.currentMeeting = await api.getMettingInfo({ meetingId: state.meetingId });
      } finally {
        state.loading = false;
      }
    };

    const handleTypeChange = (currentOperateType: OperateType) => {
      state.currentOperateType = currentOperateType;
    };

    const commitEdit = async () => {
      state.loading = true;
      await api.release({ id: state.meetingId });
      state.currentOperateType = 'review';
      regetMeeting();
      load();
    };

    const cancelEdit = () => {
      state.currentOperateType = 'review';
      regetMeeting();
    };

    const search = async (searchValue: string) => {
      state.searchValue = searchValue;
      state.isSearch = !!searchValue; // 
      fetchList();
    };

    const colsChange = (showCols: Col[]) => {
      state.showCols = showCols;
    };

    const editColsChange = (editCols: Col[]) => {
      state.editCols = editCols;
    };

    const processChange = (uploadProcess: Process) => {
      state.uploadProcess = uploadProcess;
    };


    const colsNumChange = (colsNum: 4 | 8 | 16) => {
      state.colsNum = colsNum;
    };

    const openColList = (currentCol: Col) => {
      state.currentCol = currentCol;
      state.isSearch = false;
      fetchList();
    };

    const resetSearch = () => {
      state.isSearch = false;
      state.searchValue = '';
    };

    const toCols = () => {
      state.currentCol = {} as Col;
      state.list = [];
      resetSearch();
    };

    const toEdit = () => {
      state.currentOperateType = 'edit';
    };


    const init = async () => {
      setHeight();
      load();
    };


    const refresh = () => {
      fetchList();
    };

    onMounted(() => {
      init();
    });


    return {
      ...toRefs(state),
      header,
      isEdit,
      handleTypeChange,
      cancelEdit,
      commitEdit,
      search,
      colsChange,
      editColsChange,
      colsNumChange,
      openColList,
      toCols,
      toEdit,
      mainStyle,
      refresh,
      title,
      showBackBtn,
      processChange,
      bgStyle,
      bgLoading,
    };
  },
});
