const getHandbagInfo = async (handbagId: string) => {
  return axios.get('/bff-center/showCommon/handbagInfoSearch/getHandbagInfoAndTags', {
    params: {
      handbagId,
    },
    headers: {
      language: i18n.language,
    },
  });
};

// 
export async function getInfo(relateId: string, relateType: 0 | 2) {
  let res;
  const model = relateType === 0 ? new ShoeClass() : new HandbagClass();
  if (relateType === 0) {
    res = (await getModelInfo(relateId)).data;
  } else {
    res = (await getHandbagInfo(relateId)).data;
  }
  console.log(res);
  const selectedKey = keys(model);
  remove(selectedKey, 'id');
  const value = extract(res, selectedKey);
  return value;
}

//
export async function submitModelCore({
  modelData, parentFolderId, sort, meetingColumnId, editItem,
}:{modelData:any, parentFolderId:string, sort:number, meetingColumnId:string, editItem?:Item}) {
  const {
    oldRelateId, relateType, curtailImg,
  } = modelData;
  const modelRelateInfo = await getInfo(oldRelateId, relateType) || {};
  let model:{[any:string]:any} = relateType === 0 ? new ShoeClass() : new HandbagClass();
  if (editItem) {
    // 
    const { binaryFileInfoList, imgList, form } = await getEditInfo(editItem);
    model = await getEditRelateInfo({
      binaryFileInfoList,
      form,
      modelInfo: editItem,
      imgList,
    });
  }
  const params = {
    id: editItem?.id || '',
    relateInfo: {
      ...model,
      ...modelData,
      ...modelRelateInfo,
      imgList: [...modelData.imgList, ...(model.imgList).map(item => ({ ...item, isCover: 0 }))],
      curtailImg,
      parentFolderId,
      isPublished: false,
      // UploadModelType: form.fileType === 0 ? 'SHOE' : 'HANDBAG',
      // modelNumber: form.name,
      // relateType: form.fileType,
    },
    coverImage: curtailImg,
    sort,
    meetingColumnId,
    synchronizationModel: !!editItem,
  };
  const { data: apiData } = await axios({
    url: '/meeting/column/item/addOrUpdate',
    method: 'POST',
    data: params,
  });

  Message.success(editItem ? i18n.t('virtual-meetup.public.upload_success') : i18n.t('virtual-meetup.public.upload_add_success'));

  return apiData;
}


async function getMeetingInfo(meetingId: string) {
  const res = (await axios.get<{ id: string, entryId: string, name: string }>('/meeting/meeting/info', { params: { meetingId } })).data;
  return res;
}

