  fileType: validator2ElemRule(isFilled, { message: i18n.t('virtual-meetup.dialog.fillType') }),
};
type Model = (Shoe | Handbag) & { UploadModelType: 'SHOE' | 'HANDBAG' };

const idObj = new GetId();

