const CreateSku = defineComponent({
  dialogify: {
    props: {
      // title: i18next.t('virtual-meetup.dialog.addPhoto'),
      width: '587px',
      flex: 'column',
      top: 'auto',
      bottom: 'auto',
      compact: true,
      closeOnClickModal: false,
      // size: 'medium',
    },
  },
  props: {
    meetingColumnId: {
      type: String,
    },
    sort: {
      type: Number,
    },
    meetingId: {
      type: String,
      required: true,
    },
    imgListFromWS: {
      type: Array as () => AlbumItem[],
    },
    defaultAttrs: {
      type: Object,
      default: () => ({}),
    },
    outDragOptions: {
      type: Object as PropType<{
        isIdeationDrop: boolean,
        files: File[],
        ideationDragData:IdeationDragData,
      }>,
    },
    processStart: {
      type: Function as PropType<({ coverImage, temId }:{coverImage:AlbumItem, temId:string})=>AbortController>,
      required: true,
    },
    onprocess: {
      type: Function as PropType<({ uploaded, total, temId }:{uploaded:number, total:number, temId:string})=>void>,
      required: true,
    },
    uploadDone: {
      type: Function as PropType<(item:{[any:string]:any}, temId:string)=>void>,
      required: true,
    },
    abortController: {
      type: Object as PropType<AbortController>,
    },
  },
  render() {
    const s = this as InstanceType<typeof CreateSku>;
    return (
      <div style={{ padding: '30px 30px' }}>
        <div class="flex-vertical-center title">
          <div class="el-dialog__title">{this.step === 1 ? i18next.t('virtual-meetup.dialog.addPhoto')
            : i18next.t('virtual-meetup.dialog.completeInfo')}</div>
        </div>
        {
          this.step === 1
            ? <div>
              <div class="display flex-justify-center">
                <div class={{
                  'click-icon cursor-pointer': true,
                  active: this.albumType === 0,
                }} onclick={
                  () => s.nextStep(0)
                } >
                  <div class={{
                    'icon-btn flex-center': true,
                    active: this.albumType === 0,
                  }}>
                    <i class="iconfont icondiannao" />
                  </div>
                  <div>{i18next.t('virtual-meetup.dialog.addFromLocal')}</div>
                </div>
                <div class={{
                  'click-icon cursor-pointer': true,
                  active: this.albumType === 1,
                }} onclick={
                  () => s.nextStep(1)
                } >
                  <div class={{
                    'icon-btn flex-center': true,
                    active: this.albumType === 1,
                  }}>
                    <i class="iconfont iconlogo" />
                  </div>
                  <div>{i18next.t('virtual-meetup.dialog.addFromIdetail')}</div>
                </div>
              </div>
              <div class="text-info flex-center">{i18next.t('virtual-meetup.dialog.support')}</div>
            </div> : <div>
              <el-form label-position="top" ref="formRef" props={{
                model: this.form,
              }} rules={formRules}>
                <el-form-item label={i18next.t('virtual-meetup.dialog.fillAblum')} required prop="name">
                  <el-input vModel={this.form.name} />
                </el-form-item>
                <el-form-item label={i18next.t('virtual-meetup.dialog.selectRelateType')} required prop="fileType">
                  <div class="flex-vertical-center">
                    <CheckboxBtn
                      round
                      value={s.form.fileType === 0}
                      onchange={() => { s.form.fileType = 0; }}>
                      {i18next.t('virtual-meetup.dialog.shoe')}
                    </CheckboxBtn>
                    <CheckboxBtn
                      round
                      value={s.form.fileType === 3}
                      onchange={() => { s.form.fileType = 3; }}>
                      {i18next.t('virtual-meetup.dialog.hangbag')}
                    </CheckboxBtn>
                  </div>
                </el-form-item>

                <el-form-item prop="imgList">
                  <Album types={['jpeg', 'jpg', 'png']} vModel={s.form.imgList} ref="albumRef"
                    on={{
                      'update:albumList': (e) => { console.info(e); },
                    }}
                  ></Album>
                </el-form-item>
              </el-form>

              <div class="form-submit-buttons">
                <el-button onclick={
                  () => {
                    s.step = 1;
                  }
                } type="default" round>{i18next.t('virtual-meetup.dialog.goback')}</el-button>
                <el-button onclick={
                  () => s.submit()
                } type="primary" round>{i18next.t('virtual-meetup.dialog.confirm')}</el-button>

              </div>
            </div>
        }
      </div>
    );
  },
  setup(props, { root, emit }) {
    const step = ref<1 | 2>(1);
    const isUploading = ref(false);
    const albumType = ref<number>();
    const albumRef = ref<InstanceType<typeof Album>>(null);
    const total = ref<number>(0);
    const uploaded = ref<number>(0);
    const form = reactive<{
      name: string,
      fileType: number | undefined
      imgList: AlbumItem[]
    }>({
      name: '',
      fileType: undefined,
      imgList: [],
    });
    const dataFromIedation = ref<any>();
    // const name = ref('');
    // const fileType = ref();
    const parentFolderId = useLoader(async ({ meetingId }) => {
      if (!meetingId) return undefined;
      const { entryId } = await getMeetingInfo(props.meetingId);
      return entryId;
    }, {
      ...toRefs(props),
    });
    const formRef = ref<ElForm>();

    async function submit() {
      if (albumType.value === 1) {
        try {
          const modelRelateInfo: any = {};
          const { oldRelateId, relateType, curtailImg } = dataFromIedation.value;
          if (oldRelateId && !form.fileType) {
            // 
            const apiData = await submitModelCore({
              modelData: dataFromIedation.value,
              parentFolderId: parentFolderId.value!,
              sort: props.sort!,
              meetingColumnId: props.meetingColumnId!,
            });
            emit('confirm', apiData);
          } else if (!oldRelateId && form.fileType !== undefined) {
            await validateForm(formRef.value!);
            // 
            const model = form.fileType === 0 ? new ShoeClass() : new HandbagClass();


            const data = {
              relateInfo: {
                ...model,
                ...dataFromIedation.value,
                curtailImg,
                modelNumber: form.name,
                relateType: form.fileType,
                parentFolderId: parentFolderId.value,
                isPublished: false,
              },
              coverImage: curtailImg,
              sort: props.sort,
              meetingColumnId: props.meetingColumnId,
            };
            console.log(data);
            const { data: apiData } = await axios({
              url: '/meeting/column/item/addOrUpdate',
              method: 'POST',
              data,
            });
            emit('confirm', apiData);
          } else {
            step.value = 2;
            albumType.value = 0;
            submit();
          }
        } catch (e) {
          root.$errHandler(e);
        }
      } else {
        // 
        await validateForm(formRef.value!);
        if (form.imgList.length === 0) {
          throw new Error('');
        }
        const model = form.fileType === 0 ? new ShoeClass() : new HandbagClass();
        const temId = `${idObj.get()}`;
        const coverImageItem = form.imgList.find(i => i.isCover === 1) || form.imgList[0];
        coverImageItem.isCover = 1;
        const abortController = props.processStart({ coverImage: coverImageItem, temId });
        emit('confirm');
        await albumRef.value.upload(({ uploaded: _uploaded, total: _total }) => {
          props.onprocess({ uploaded: _uploaded, total: _total, temId });
        }, abortController);


        const data = {
          relateInfo: {
            ...model,
            parentFolderId: parentFolderId.value,
            // UploadModelType: form.fileType === 0 ? 'SHOE' : 'HANDBAG',
            modelNumber: form.name,
            relateType: form.fileType!,
            imgList: form.imgList,
            curtailImg: coverImageItem.path,
            isPublished: false,
          },
          coverImage: coverImageItem.path,
          sort: props.sort,
          meetingColumnId: props.meetingColumnId,
        };

        const { data: apiData } = await axios({
          url: '/meeting/column/item/addOrUpdate',
          method: 'POST',
          data,
        });

        props.uploadDone(apiData, temId);

        // root.$alert('');
      }
    }

    async function nextStep(type: 0 | 1) {
      albumType.value = type;
      // 1
      if (albumType.value === 1) {
        dataFromIedation.value = await root.$injectDialog({
          is: FileSelect,
          props: {
            fileType: form.fileType,
            name: form.name,
            albumType: albumType.value,
            meetingId: props.meetingId,
            outDragOptions: props.outDragOptions,
          },
        });
        console.info(dataFromIedation);
        if (dataFromIedation.value.relateType === 2) {
          step.value = 2;
          form.imgList = dataFromIedation.value.imgList;
          return;
        }
        await submit();
      } else {
        step.value = 2;
      }
    }

    const handleOutDrag = async () => {
      if (props.outDragOptions) {
        const { isIdeationDrop, files, ideationDragData } = props.outDragOptions;

        if (isIdeationDrop) {
          // ws
          const {
            workspaceId, data, dataType, activeFolder: _activeFolder,
          } = ideationDragData;
          if (dataType === 'img') {
            nextStep(0);
            await root.$nextTick();
            // 
            const { album } = albumRef.value;
            const imgList = catalogs2imgList(data);
            album.push(...imgList);
          } else {
            // 
          }
        } else {
          nextStep(0);
          await root.$nextTick();
          // 
          const { files2albums, album } = albumRef.value;
          const [_files, albums] = filterFileAndAlbum(files);
          // album
          album.push(...albums);
          // form.imgList = [...form.imgList, ...albums]; //AlbumJSON.Parse,symbol
          // 
          files2albums(_files);
        }
      }
    };

    onMounted(() => {
      if (props.imgListFromWS) {
        form.imgList = props.imgListFromWS;
      }
      handleOutDrag();
    });

    return {
      step,
      submit,
      nextStep,
      formRef,
      form,
      albumType,
      albumRef,
      total,
      uploaded,
      isUploading,
      dataFromIedation,
    };
  },
});


export default CreateSku;
