
import { defineComponent, ref } from '@vue/composition-api';
import Jsonschema2Form from '@4dst-saas/form-utils/dist/components/jsonschema2form';
import Jsonschema from '@4dst-saas/form-utils/dist/components/jsonschema2form/JSONschema';
// import TagsEditor from '@/components/base/TagsEditor.vue';
import isFilled from '@/utils/validators/is-filled';

import { i18n } from '@/i18n';
import schema from './handbagInfoSchema';

import StyleForm from './styleForm.vue';
import Tags from './Tags.vue';

const HandbagForm = defineComponent({
  name: 'HandbagForm',
  components: {
    Tags,
    StyleForm,
  },
  props: {
    jsonschema: {
      type: Array as () => Jsonschema[],
      default: () => schema,
    },
    form: {
      type: Object,
      required: true,
    },

  },
  setup() {
    // const form = ref({});
    const formRef = ref(null);

    const validate = () => {
      return formRef.value.validate();
    };

    return {
      // form,
      formRef,
      validate,
    };
  },

  render() {
    return <div>
      <div style="font-size: 16px;color: #222222;padding-bottom: 10px;">{i18n.t('up-meeting.detail.product_info')}</div>
      <Jsonschema2Form
        ref='formRef'
        jsonschema={this.jsonschema}
        class="form"
        size="small"
        formItemValidator={{ isFilled }}
        form={this.form}
        labelPosition='top'>
      </Jsonschema2Form>
    </div>;
  },
});

export default HandbagForm;
