import useResize from '@/utils/composables/use-resize';
import {
  reactive, computed, ref, toRefs, getCurrentInstance, onMounted, watch, SetupContext,
} from '@vue/composition-api';

// const getAppMinWidth = () => {
//   const App = document.querySelector('#app');
//   if (!App) return 0;
//   const minWidth = +window.getComputedStyle(App).minWidth.slice(0, -2);
//   return minWidth;
// };

const useCols = (inicColsNum:number) => {
  const _this:any = getCurrentInstance();
  const state = reactive({
    currentColsNum: inicColsNum,

  });
  const review = ref(null);
  watch(() => _this.colsNum, () => {
    // if (!review.value || _this.colsNum < 16) {
    //   state.currentColsNum = _this.colsNum;
    // }
    // 1000px16
    // state.currentColsNum = (review.value! as HTMLDivElement).clientWidth > 1000 ? _this.colsNum : 8;
    state.currentColsNum = _this.colsNum;
  });
  const colWidth = computed(() => 100 / state.currentColsNum);
  const colHeight = ref(0);
  const setColHeight = () => {
    const { clientWidth } = document.querySelector('#app') || document.documentElement;
    const MARGIN_PADDING_WIDTH = 80;
    const ADD_COL_WIDTH = state.currentColsNum === 16 ? 10 : 30;
    colHeight.value = (clientWidth - MARGIN_PADDING_WIDTH) / state.currentColsNum - ADD_COL_WIDTH;
  };
  const colWidthStyle = computed(() => ({
    width: `${colWidth.value}%`,
  }));

  const itemHeightStyle = computed(() => ({
    height: `${colHeight.value}px`,
  }));

  setColHeight();

  watch(() => state.currentColsNum, () => {
    setColHeight();
  });

  useResize(setColHeight);

  return {
    ...toRefs(state),
    review,
    colWidth,
    colWidthStyle,
    itemHeightStyle,
  };
};

export default useCols;
