import axios from '@/utils/axios';

const api = {
  // 
  async getMettingInfo(params:{meetingId:string}) {
    const meetingInfo = (await axios({
      url: '/meeting/meeting/info',
      params,
    })).data || {};
    return meetingInfo;
  },
  // 
  async delItem(params:{id:string}) {
    const result = (await axios({
      url: '/meeting/column/item/delete',
      params,
    })) || false;

    return result;
  },
  // 
  async moveItem(params:{id:string, meetingColumnId:string, index:number}) {
    const result = (await axios({
      url: '/meeting/column/item/move',
      method: 'post',
      data: params,
    })) || false;

    return result;
  },

  // 
  async fetchReviewCols(params:{meetingId:string, meetingColumnIds?:string[], meetingColumnPage?:number, meetingColumnSize?:number}) {
    const result = (await axios({
      url: '/meeting/conference/console',
      method: 'post',
      data: params,
    }))?.data || {};

    return result;
  },

  // 
  async fetchSearch(params:{condition:string, catalogId:string}) {
    const result = (await axios({
      url: '/meeting/conference/console/search',
      method: 'post',
      data: params,
    }))?.data || [];

    console.log(result);

    return result;
  },
  // 
  async fetchEditCols(params:{id:string}) {
    const result = (await axios({
      url: '/meeting/conference/console/editable',
      method: 'post',
      data: params,
    }))?.data || [];

    return result;
  },
  // 
  async release(params:{id:string}) {
    const result = (await axios({
      url: '/meeting/conference/console/release',
      method: 'post',
      data: params,
    })) || false;

    return result;
  },
  // 
  async addCol(params:{ name:string, sort:number, meetingId:string}) {
    const id = (await axios({
      url: '/meeting/column/add',
      method: 'post',
      data: params,
    }))?.data || '';

    return id;
  },
  // 
  async delCol(params:{columnId:string}) {
    const result = (await axios({
      url: '/meeting/column/delete',
      params,
    }));

    return result;
  },
  // 
  async editCol(params:{id:string, name:string, sort:number, meetingId:string}) {
    const result = (await axios({
      url: '/meeting/column/update',
      method: 'post',
      data: params,
    }));

    return result;
  },
  // 
  async moveCol(params:{sorts:{id:string, sort:number}[]}) {
    const result = (await axios({
      url: '/meeting/column/update/sort',
      method: 'post',
      data: params,
    }));

    return result;
  },
};

export default api;
