




























































































































































































import {
  defineComponent, reactive, computed, ref, toRefs, watch, onMounted, onUnmounted,
} from '@vue/composition-api';
import {
  DragContainer, DragList, DragItem, getId,
} from '@/components/drags';
import OverText from '@/components/OverText.vue';
import BaseImage from '@/components/base/Image.vue';
import { i18n, i18next } from '@/i18n';
import { ElInput } from 'element-ui/types/input';
import delay from '_/delay';
import { AlbumItem } from '@/models/Catalog';
import CreateSku from '../components/CreateSku.vue';
import EditModel from '../modelInfo/index.vue';
import { Meeting } from '../typings';
import useListDrag from './useListDrag';
import useOutDrag from './useOutDrag';
import api from './api';
import { Item, Col } from './index.vue';
import useProcess from './useProcess';

function makeUnloadWarn() {
  const bind = (e:BeforeUnloadEvent) => {
    // Cancel the event
    e.preventDefault(); // If you prevent default behavior in Mozilla Firefox prompt will always be shown
    // Chrome requires returnValue to be set
    e.returnValue = '';
  };
  window.addEventListener('beforeunload', bind);
  return function off() {
    const makeSureUnbind = (e:BeforeUnloadEvent) => {
      // the absence of a returnValue property on the event will guarantee the browser unload happens
      delete e.returnValue;
      window.removeEventListener('beforeunload', makeSureUnbind);
    };
    window.removeEventListener('beforeunload', bind);
    window.addEventListener('beforeunload', makeSureUnbind);
  };
}

export default defineComponent({
  name: 'meet-home__edit',
  components: {
    DragContainer, DragList, OverText, BaseImage,
  },
  props: {
    meetingId: {
      type: String,
    },
    meeting: {
      type: Object as unknown as () => Meeting,
      required: true,
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      currentCols: [] as any[],
      itemDraging: false,
      dragStartItem: {} as Item,
      isOutDrag: false,
      unloadEvent: null as Function |null,
    });
    const {
      listStartIndex, listEnterIndex, listDraging, listDragStart, listDragEnter, listDragEnd,
    } = useListDrag();

    const {
      dragInfo, containerEnter, containerDrop, process: dragProcess, cancelUpload: dragCancelUpload,
    } = useOutDrag();

    const {
      process: localProcess, normalProcessStart, onprocess, uploadDone, cancelUpload: localCancelUpload,
    } = useProcess({ isOutDrag: false });

    const cancelUpload = (item:Item) => {
      const { isOutDrag } = state;
      if (isOutDrag) {
        dragCancelUpload(item, dragProcess.value);
      } else {
        localCancelUpload(item, localProcess.value);
      }
    };


    const process = computed(() => ({ ...dragProcess.value, ...localProcess.value }));
    watch(process, (_process) => {
      emit('process-change', _process);
    });


    const dragListVms = ref<InstanceType<typeof DragList>[]>([]);
    const dragContainer = ref<InstanceType<typeof DragContainer>>();
    const colNameInputes = ref<InstanceType<typeof ElInput>[]>([]);

    const isDraging = computed(() => state.itemDraging || listDraging.value);


    watch(() => state.currentCols, () => {
      emit('cols-change', state.currentCols);
    });

    onUnmounted(() => {
      state.unloadEvent?.();
    });

    const setCol2Vm = (colIndex?: number, col?: Col) => {
      if (colIndex && col) {
        dragListVms.value[colIndex].col = col;
        return;
      }
      // colvm.id
      dragListVms.value.forEach((vm: any, i) => {
        vm.col = state.currentCols[i];
      });
    };

    const addItem = async (col: Col, colIndex: number) => {
      const apiData = await root.$injectDialog({
        is: CreateSku,
        props: {
          meetingColumnId: col.id,
          sort: col.itemInfos.length,
          meetingId: props.meetingId,
          processStart: ({ coverImage, temId }:{coverImage:AlbumItem, temId:string}) => {
            state.isOutDrag = false;
            return normalProcessStart({
              coverImage, temId, col,
            });
          },
          onprocess,
          uploadDone,
        },
      });
      // col.itemInfos.push(sku);
      if (apiData) {
      // ideationsku
        col.itemInfos.push(apiData);
      }
    };

    const editItem = async (item: Item, col: Col, itemIndex: number) => {
      await root.$injectDialog({
        is: EditModel,
        props: {
          modelInfo: { ...item, sort: itemIndex },
          meetingId: props.meetingId,
          processStart: ({ coverImage, temId }:{coverImage:AlbumItem, temId:string}) => {
            state.isOutDrag = false;
            return normalProcessStart({
              coverImage, temId, col, item,
            });
          },
          onprocess,
          uploadDone,
        },
      }, {
        key: (new Date()).valueOf(),
      });
      // if (typeof sku === 'object') {
      //   root.$set(col.itemInfos, itemIndex, sku);
      // }
    };

    const delItem = async (item: Item, col: Col, itemIndex: number) => {
      await root.$confirm(i18n.t('public.dialog.sure_delete'), i18n.t('public.dialog.prompt'));
      await api.delItem({ id: item.id });
      col.itemInfos.splice(itemIndex, 1);
    };

    const addCol = async (index: number) => {
      const name = i18next.t('virtual-meetup.home.new_col');
      const newCol = {
        name,
        temName: name,
        isInput: false,
        itemInfos: [],
      };
      const id = await api.addCol({ name: newCol.name, sort: index, meetingId: props.meetingId! });
      const _newCol = { id, ...newCol };
      state.currentCols.splice(index + 1, 0, _newCol);
      await root.$nextTick();

      setCol2Vm(); // dragListVmsindex
      dragContainer.value?.init();
    };

    const delCol = async (col: Col, index: number) => {
      await root.$confirm(i18n.t('public.dialog.sure_delete'), i18n.t('public.dialog.prompt'));
      await api.delCol({ columnId: col.id });
      state.currentCols.splice(index, 1);
      await root.$nextTick();
      dragContainer.value?.init({ addEvents: false });
    };

    const editColTitle = async (col: Col, i: number) => {
      col.isInput = true;
      await root.$nextTick();
      colNameInputes.value?.[0].focus();
    };

    const blurColTitle = async (col: any, i: number) => {
      col.isInput = false;
      if (col.temName === col.name) return;
      await api.editCol({
        id: col.id!, name: col.temName, sort: i + 1, meetingId: props.meetingId!,
      });
      col.name = col.temName;
    };


    const containerStart = ({ item, vm }: { item: Item, vm: any }) => {
      state.itemDraging = true;
      state.dragStartItem = item;
    };

    const swap = (list: any[], start: number, end: number) => {
      if (start === end) return;
      const startItem = list.splice(start, 1)[0];
      list.splice(end, 0, startItem);
    };


    const containerDragEnd = async ({
      startIndex, endVm, isInside, cross, inside,
    }: {
      startIndex: number,
      endIndex: number,
      endVm: any,
      isInside: boolean,
      cross: { startIndex: number, endIndex: number },
      inside: { startIndex: number, enterIndex: number }
    }) => {
      state.itemDraging = false;

      if (isInside) {
        // 
        const col = state.currentCols[startIndex];
        try {
          if (inside.enterIndex === -1) return; // 
          await api.moveItem({ id: state.dragStartItem.id, meetingColumnId: col.id, index: inside.enterIndex });
        } catch (error) {
          // 
          swap(col.itemInfos, inside.startIndex, inside.enterIndex);
          throw error(error);
        }
      } else {
        // ;
        const toCol = endVm.col;

        try {
          if (cross.endIndex === -1) return; // 
          await api.moveItem({ id: state.dragStartItem.id, meetingColumnId: toCol.id, index: cross.endIndex });
        } catch (error) {
          // 
          const fromCol = state.currentCols[startIndex];
          const item = toCol.itemInfos.splice(cross.endIndex, 1)[0];
          fromCol.itemInfos.splice(cross.startIndex, 0, item);
        }
      }
    };


    const init = async () => {
      try {
        emit('loading', true);
        const cols: Col[] = await api.fetchEditCols({ id: props.meetingId! });
        state.currentCols = cols.map(item => ({ ...item, temName: item.name, isInput: false }));
        await root.$nextTick();
        setCol2Vm();
        state.unloadEvent = makeUnloadWarn();
      } finally {
        emit('loading', false);
      }
    };


    init();


    return {
      ...toRefs(state),
      isDraging,
      addItem,
      editItem,
      delItem,
      addCol,
      delCol,
      listStartIndex,
      listEnterIndex,
      listDragStart,
      listDragEnter,
      listDragEnd,
      listDraging,
      editColTitle,
      blurColTitle,
      containerStart,
      containerDragEnd,
      dragListVms,
      dragContainer,
      colNameInputes,
      dragInfo,
      containerEnter,
      containerDrop,
      dragProcess,
      localProcess,
      process,
      cancelUpload,
    };
  },
});
