
import { defineComponent, onMounted, ref } from '@vue/composition-api';
import { getDictByType } from '@/loaders/dictionary';

const styleForm = defineComponent({
  name: 'StyleForm',
  props: {
    value: Number,
  },
  // model:{

  // }
  setup() {
    const options = ref([]);

    onMounted(async () => {
      options.value = await getDictByType({ type: 'handbagType' });
    });

    return {
      options,
    };
  },

  render() {
    const { options } = this;
    return <el-select value={this.value} onchange={(e) => { this.$emit('input', e); }}>
      {
        options.map(item => {
          return <el-option key={item.name}
            label={item.name}
            value={item.value}></el-option>;
        })
      }
    </el-select>;
  },
});

export default styleForm;
