
import { defineComponent, ref } from '@vue/composition-api';

interface TagItem {
  id?: string,
  tag: string,
}

const Tags = defineComponent({
  name: 'Tags',
  model: {
    prop: 'tags',
    event: 'input',
  },
  props: {
    // 
    tags: {
      type: Array as () => String[],
      default: () => ([]),
    },
    // ID
    // modelId: {
    //   type: String,
    //   required: true,
    // },
    // 0 1 2 3 4
    relateType: {
      type: Number,
    },

    // 
    // editable: {
    //   type: Boolean,
    //   default: false,
    // },
    // 
    maxlength: {
      type: Number,
      default: 30,
    },
    // 
    maxTagsLen: {
      type: Number,
      default: 10,
    },
  },
  setup(props, { emit, root: { $nextTick } }) {
    const inputValue = ref('');
    const inputVisible = ref(false);
    const saveTagInput = ref(null);

    // 
    async function showInput() {
      inputVisible.value = true;
      await $nextTick();
      console.info(saveTagInput.value);
      (saveTagInput.value as any).$refs.input.focus();
    }

    function handleInputConfirm() {
      // debugger;
      if (inputValue.value.trim()) {
        emit('input', [...props.tags, inputValue.value]);
      }
      inputVisible.value = false;
      inputValue.value = '';
    }
    return {
      handleInputConfirm,
      inputValue,
      inputVisible,
      saveTagInput,
      showInput,
    };
  },

  render() {
    const {
      tags, maxTagsLen, maxlength, showInput, inputVisible, handleInputConfirm,
    } = this;

    return <div class="tags-editor-wrapper">
      {
        tags.map((tag, index) => {
          return <el-tooltip
            effect="dark"
            key={index}
            content={tag}>
            <el-tag
              class="flex-center"
              closable
              disable-transitions={false}
              onclose={() => { tags.splice(index, 1); }}>
              <span style="max-width: 150px;display:inline-block" class="text-one-line">
                {tag}
              </span>
            </el-tag>
          </el-tooltip>;
        })
      }
      {

        // if (tags.length < maxTagsLen) {
        //   if(inputVisible) {
        //     retrun <el-input
        //       class="input-new-tag"
        //       ref="saveTagInput"
        //       size="mini"
        //       maxlength={maxlength}
        //       onkeyup={e => { e.keyCode === 13 && this.$emit('change', [...tags, e.target.value]); }}
        //       onblur={this.$emit('change', [...tags, e.target.value])} />
        //   }else {
        //     return <i class="iconfont icon-add button-new-tag" onclick={showInput} />
        //   }
        // }
      }
      {

        // eslint-disable-next-line no-nested-ternary
        tags.length < maxTagsLen
          ? inputVisible
            ? <el-input
              class="input-new-tag"
              ref="saveTagInput"
              size="mini"
              vModel={this.inputValue}
              maxlength={maxlength}
              nativeOnkeyup={e => { e.keyCode === 13 && handleInputConfirm(); }}
              onblur={() => { handleInputConfirm(); }} />
            : <i class="iconfont icon-add button-new-tag" onclick={showInput} />
          : null
      }
    </div>;
  },
});

export default Tags;
