

























import {
  defineComponent, reactive, computed, ref, toRefs, PropType,
} from '@vue/composition-api';
import OverText from '@/components/OverText.vue';
import BaseImage from '@/components/base/Image.vue';
import openWindow from '@/utils/open-window';
import axios from '@/utils/axios';
import { Item } from '../index.vue';

export default defineComponent({
  name: 'meet-review__item',
  components: { OverText, BaseImage },
  props: {
    data: {
      type: Object as PropType<Item>,
      default: () => ({}),
    },
    colsNum: {
      type: Number as () => 4 | 8 | 16,
      default: 8,
    },
    meetingId: {
      type: String,
      required: true,
    },
    imgStyle: {
      type: Object,
      default: () => ({}),
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({});
    const toDetail = async (item: Item) => {
      const params = {
        columnId: item.meetingColumnId,
        meetingId: props.meetingId,
        itemId: item.id,
      };
      root.$router.push({ path: '/up-meeting/detail', query: params });
    };
    const toggleLike = async () => {
      const { data } = props;
      const { like } = data;
      const params = {
        relateId: data.id,
        relateType: 0,
      };
      const url = `meeting/userLoveStatistics/${like.userLoveStatus ? 'cancelUserLoveStatus' : 'addUserLoveStatus'}`;
      await axios.post(url, params);
      like.userLoveStatus = !like.userLoveStatus;
      like.userLoveCount += like.userLoveStatus ? 1 : -1;
    };
    return {
      ...toRefs(state),
      toDetail,
      toggleLike,
    };
  },
});
