
import {
  defineComponent,
} from '@vue/composition-api';
import FolderTree from '@/views/file-list/components/catalog/FolderTree.vue';
import { CatalogModel, SearchParams } from '@/models/Catalog';
import { useCatalog } from '../utils/useCatalog';
import Panellist from './List.vue';

type SearchParamsWithReleatType = SearchParams & { relateType: number | number[] };

export default defineComponent({
  props: {
    id: {
      type: String,
      required: true,
    },
    workspaceId: {
      type: String,
    },
    workspaceName: {
      type: String,
    },
    workspaceType: {
      type: Number,
    },
    workspaceBusinessType: {
      type: Number,
    },
    searchParams: {
      type: Object as unknown as () => SearchParamsWithReleatType,
    },
    entryAppType: {
      type: Number,
      required: true,
    },
    selectedCatalog: {
      type: Array as () => CatalogModel[],
      default: [],
    },
  },
  render() {
    const s = this;
    return (
      <section class="flex-1 flex select-container">
        <FolderTree
          ref="folderTree"
          visible={true}
          class="folder-tree"
          onSelect={(item: CatalogModel) => this.$emit('enterFolder', item)}
          paths={this.paths}
        />
        <Panellist
          relateType={this.searchParams?.relateType ?? 2}
          class="panel-list"
          catalogs={this.catalogs}
          selectedCatalog={this.selectedCatalog}
          canLoadMore={this.canLoadMore}
          loadingPage={this.loadingPage}
          loadMore={this.loadMore}
          onselect-catalog={
            (catalog: CatalogModel) => {
              this.$emit('select-catalog', catalog);
            }
          }
          scopedSlots={{
            empty: () => (!this.catalogPaging ? null : <error error={{
              code: 404,
              message: this.emptyTips,
            }} style="font-size: 14px;height: 100%;" img={this.isSearch ? require('@/assets/img/error/no-result.png') : undefined} />),
          }}
        />
      </section>
    );
  },
  setup(props) {
    const {
      paths, catalogs, catalogPaging, emptyTips, isSearch,
      activeFolder, loadMore, canLoadMore, loadingPage,
    } = useCatalog(props);

    console.log(paths);
    return {
      paths,
      catalogs,
      catalogPaging,
      emptyTips,
      isSearch,
      activeFolder,
      canLoadMore,
      loadMore,
      loadingPage,

    };
  },
});
