
import { CatalogModel } from '@/models/Catalog';
import { defineComponent } from '@vue/composition-api';
import BaseImage from '@/components/base/Image.vue';
import Intersect from 'vue-intersect';
import { i18n } from '@/i18n';


export function isImage(name: string, relateType: number) {
  if (relateType !== 2) return true;
  const nameArr = name.split('.');
  const types = ['jpg', 'jpeg', 'png'];
  const len = nameArr.length;
  if (len) {
    const ext = nameArr[len - 1];
    return types.includes(ext);
  }
  return false;
}

export default defineComponent({
  props: {
    catalogs: {
      type: Array as () => CatalogModel[],
      required: true,
    },
    relateType: {
      type: Number,
      required: true,
    },
    selectedCatalog: {
      type: Array as () => CatalogModel[],
    },
    canLoadMore: {

    },
    loadingPage: {
      type: Number,
    },
    loadMore: {
      type: Function,
    },
  },
  render() {
    const { catalogs, $scopedSlots } = this;
    const Empty = catalogs && catalogs.length === 0 ? $scopedSlots.empty?.(null) : null;
    return (
      <div class="scrollable list-container scrollable-y">
        {
          Empty || this.catalogs.map(catalog => {
            return this.relateType === 2 ? <div class={{
              'list-item': true,
              active: this.selectedCatalog?.find(
                selected => selected.id === catalog.id,
              ),
            }} onclick={
              () => {
                if (isImage(catalog.name, this.relateType)) {
                  this.$emit('select-catalog', catalog);
                }
              }
            }>
              <i class="iconfont iconchenggong selected-icon" vShow={this.relateType === 2} />
              <i class="iconfont iconyuanxing" />
              <BaseImage fit="contain"
                thumb={{ width: 96, height: 96 }}
                src={catalog.contentThumbnail}
                default-image="defaultImageSrc"
                class={{
                  img: true,
                  active: this.selectedCatalog?.find(
                    selected => selected.id === catalog.id,
                  ),
                  disabled: !isImage(catalog.name, this.relateType),
                }}
              />
              <div class="text-two-line">{catalog.name}</div>
            </div> : <div class={
              {
                'list-item': true,
                active: this.selectedCatalog?.find(
                  selected => selected.id === catalog.id,
                ),
              }
            } onclick={
              () => {
                if (isImage(catalog.name, this.relateType)) {
                  this.$emit('select-catalog', catalog);
                }
              }
            }>
                <BaseImage fit="contain"
                  thumb={{ width: 96, height: 96 }}
                  src={catalog.fileInfo.thumbnail}
                  default-image="defaultImageSrc"
                  class={{
                    img: true,
                    disabled: !isImage(catalog.name, this.relateType),
                  }}
                />
                <div class="text-two-line">{catalog.name}</div>
              </div>;
          })
        }
        {
          this.canLoadMore ? <Intersect onEnter={this.loadMore}>
            <div class="load-more">
              {
                this.loadingPage && this.loadingPage > 1 ? [
                  <i class="el-icon-loading" />,
                  i18n.t('material-mall.loading'),
                ] : i18n.t('material-mall.scroll_to_load')
              }
            </div>
          </Intersect> : (this.catalogs.length > 0 ? <el-divider>{i18n.t('material-mall.no_more')}</el-divider> : null)

        }
      </div >
    );
  },
  setup(props) {
    console.log(props.catalogs);
    return {

    };
  },
});
