
import {
  defineComponent,
} from '@vue/composition-api';

const CheckboxBtn = defineComponent({
  name: 'CheckboxBtn',

  props: {
    value: {
      type: Boolean,
      default: false,
      require: true,
    },

  },

  setup(props, { emit }) {
    const changeCheck = (): void => {
      console.info(props.value);
      emit('change', !props.value);
    };

    return {
      changeCheck,
    };
  },

  render() {
    const self = this as InstanceType<typeof CheckboxBtn>;

    return <el-button
      round
      onClick={self.changeCheck}
      {...{ attrs: self.$attrs }}
      class={{ checked: self.value, 'checkbox-btn': true }}
    >
      {self.value
        ? <i class="iconfont icon-tick-circle"></i>
        : <div class="not-check-icon"></div>
      }
      {
        self.$slots.default
      }
    </el-button>;
  },
});

export default CheckboxBtn;
