














































































































































































import {
  defineComponent, reactive, computed, ref, toRefs, watch, onMounted,
} from '@vue/composition-api';
import { getContextUser } from '@/loaders/context';
import setLang, {
  languageNumber2lang,
  lang2TranslatedLang,
  langMapLangNum,
  langConfigList,
} from '@/router/utils/set-lang';
import { i18n, i18next } from '@/i18n';

import uploadToOSS from '@/utils/oss/uploadToOSS';
import filterThumb from '@/filters/thumb';
import urlToBlob from '@/utils/url-to-blob';
import { getRelativePathFromUrl } from '@/utils/normalize-component';
import axios from '@/utils/axios';
import User from '@/models/User';
import { Meeting } from '../typings';
import Header from '../Header.vue';
import { OperateParams, MeetForm } from '../mlist/components/OperateMeeting.vue';
import LogoPicker from '../components/LogoPicker.vue';
import { Col, Item } from './index.vue';
import { Process } from './useProcess';
import useBg from './useBg';

const editMeeting = async (params:OperateParams) => {
  const res = axios({
    url: '/meeting/meeting/update',
    method: 'post',
    data: params,
  });
  return res;
};

export type OperateType = 'review' | 'edit';

export default defineComponent({
  name: 'meet-home__header',
  components: { Header, LogoPicker },
  props: {
    meeting: {
      type: Object as unknown as () => Meeting,
      required: true,
    },
    operateType: {
      type: String as () => OperateType,
      default: 'review',
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
    searchValue: {
      type: String,
      default: '',
    },
    col: {
      type: Object,
      default: () => ({}),
    },
    allCols: {
      type: Array as () => Col[],
      default: () => [],
    },
    showCols: {
      type: Array as () => Col[],
      default: () => [],
    },
    editCols: {
      type: Array as () => Col[],
      default: () => [],
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
    commitLoading: {
      type: Boolean,
      default: false,
    },
    list: {
      type: Array as () => Item[],
      default: () => [],
    },
    process: {
      type: Object as ()=>Process,
      default: () => ({}),
    },

  },
  setup(props, { emit, root }) {
    const state = reactive({
      currentSearchValue: '',
      displayTypes: [{
        title: i18next.t('virtual-meetup.home.big_img'),
        colsNum: 4,
        icon: 'icondatu',
      }, {
        title: i18next.t('virtual-meetup.home.middle_img'),
        colsNum: 8,
        icon: 'iconzhongtu',
      }, {
        title: i18next.t('virtual-meetup.home.small_img'),
        colsNum: 16,
        icon: 'iconxiaotu',
      }],
      showDisplayTypes: false,
      colsNum: 8 as 4 | 8 | 16,
      temCurrentAllCols: [] as any[], // ,
      currentAllCols: [] as any[],
      currentCol: {} as any,
      isShowAllCols: false,
      logo: undefined as File|undefined,
      logoKey: '',
    });
    const { setBg, cancelBg } = useBg(props.meeting);
    const isShowRoom = computed(() => props.meeting?.type === 2);
    const user = computed(() => getContextUser());
    const userImage = computed(() => user.value?.headImg || require('@/assets/img/header/default_avatar.png'));
    const userTitle = computed(() => user.value?.name || user.value?.account || '');


    const setting = reactive({
      show: false,
      open() {
        state.temCurrentAllCols = JSON.parse(JSON.stringify(state.currentAllCols));
      },
      done() {
        const showCols = state.currentAllCols.filter(item => item.isShow);
        setting.show = false;
        emit('cols-change', showCols);
      },
      cancel() {
        state.currentAllCols = JSON.parse(JSON.stringify(state.temCurrentAllCols));
        setting.show = false;
      },
    });
    watch(() => props.searchValue, () => {
      state.currentSearchValue = props.searchValue;
    }, { immediate: true });
    watch(() => props.allCols, () => {
      state.currentAllCols = props.allCols.map(item => ({ ...item, isShow: true }));
      const showCols = state.currentAllCols.filter(item => item.isShow);
      emit('cols-change', showCols);
    }, { immediate: true });
    watch(() => props.col, () => {
      state.currentCol = props.col;
    });
    watch(() => state.currentAllCols, () => {
      const showCols = state.currentAllCols.filter(item => item.isShow);
      state.isShowAllCols = showCols.length === props.allCols.length;
    }, { immediate: true, deep: true });

    const initLogo = async () => {
      const { meeting } = props;
      const logo = filterThumb(meeting.logo, { width: 128, height: 128 } as any) as string;
      state.logoKey = getRelativePathFromUrl(meeting.logo);
      // urlToBlob
      if (logo) {
        state.logo = await urlToBlob(logo) as File;
      }
    };

    const logoChange = async (image:File|undefined, imgUrl:string) => {
      const { meeting } = props;
      state.logo = image;
      const updateLogo = async (imgKey:string = '') => {
        await editMeeting({
          id: meeting.id,
          logo: imgKey,
        });
      };
      if (!image) {
        state.logoKey = '';
        updateLogo();
        return;
      }
      const imgKey = await uploadToOSS(image, 'MEETING');
      updateLogo(imgKey);
      state.logoKey = imgKey;
    };

    const changeType = (operateType: OperateType) => {
      emit('type-change', operateType);
    };
    const cancelEdit = async () => {
      await root.$confirm(i18next.t('virtual-meetup.home.cancel_set_room_tip'), i18next.t('virtual-meetup.home.cancel_set_room'));
      emit('cancel-edit');
    };
    const commitEdit = async () => {
      if (Object.values(props.process).length) {
        await root.$confirm(i18next.t('virtual-meetup.home.commit_has_upload_tip'), i18next.t('virtual-meetup.home.cancel_set_room'));
      }
      const uploadingItems = Object.values(props.process);
      if (uploadingItems.length) {
        uploadingItems.forEach(item => {
          item.abortController.abort();
        });
      }
      emit('commit-edit');
    };
    const search = () => {
      emit('search', state.currentSearchValue);
    };
    const changeCols = (colsNum: 4 | 8 | 16) => {
      state.colsNum = colsNum;
      state.showDisplayTypes = false;
      emit('cols-num-change', colsNum);
    };
    const changeIsShowAllCols = (isShowAllCols: boolean) => {
      state.currentAllCols.forEach(item => { item.isShow = isShowAllCols; });
    };


    initLogo();


    return {
      ...toRefs(state),
      setting,
      changeType,
      cancelEdit,
      commitEdit,
      search,
      changeCols,
      changeIsShowAllCols,
      userImage,
      userTitle,
      langConfigList,
      isShowRoom,
      logoChange,
      setBg,
      cancelBg,
    };
  },
});
