import {
  reactive, computed, ref, toRefs, getCurrentInstance, onMounted, watch, SetupContext,
} from '@vue/composition-api';

const useWidthTransitionEnd = () => {
  const overText = ref<any[]|null>(null);
  const handleWidthTransitionend = (i:number) => {
    if (i !== 0) return; // ,OverText,colslist
    if (!Array.isArray(overText.value)) return;
    overText.value.forEach(item => item.init());
  };
  return {
    overText,
    handleWidthTransitionend,
  };
};

export default useWidthTransitionEnd;
