import { AlbumItem } from '@/models/Catalog';
import {
  reactive, computed, ref, toRefs, getCurrentInstance, onMounted, watch, SetupContext,
} from '@vue/composition-api';
import { DragInfo } from './useOutDrag';
import { Item, Col } from './index.vue';

export interface Process{
  [any:string]:{
    percent:number,
    album:AlbumItem,
    abortController:AbortController,
  },
}


const useProcess = ({ isOutDrag }:{isOutDrag:boolean}) => {
  const _this:any = getCurrentInstance();
  const state = reactive({
    process: {} as Process,
  });
  // processStart
  const dragProcessStart = ({
    coverImage, temId, dragInfo, col, item,
  }:{coverImage:AlbumItem, temId:string, dragInfo:DragInfo, col: Col, item?:Item}) => {
    const { type, itemIndex } = dragInfo;
    const isInsert = type === 'out-drag-insert-area';
    const abortController = new AbortController();
    _this.$set(state.process, temId, {
      percent: 0,
      album: coverImage,
      abortController,
    });
    if (item) {
      // 
      return state.process[temId].abortController;
    }

    const temSku = {
      id: temId,
      coverImage,
      isTempSku: true,
      isOutDrag,
      abortController,
    };
    if (isInsert) {
      col.itemInfos.splice(itemIndex, 0, temSku);
    } else {
      col.itemInfos.push(temSku);
    }
    return state.process[temId].abortController;
  };
  // processStart
  const normalProcessStart = ({
    coverImage, temId, col, item,
  }:{coverImage:AlbumItem, temId:string, col: Col, item?:Item}) => {
    const abortController = new AbortController();
    _this.$set(state.process, temId, {
      percent: 0,
      album: coverImage,
      abortController,
    });
    if (item) {
      // 
      return state.process[temId].abortController;
    }
    const temSku = {
      id: temId,
      coverImage,
      isTempSku: true,
      isOutDrag,
      abortController,
    };
    col.itemInfos.push(temSku);
    return state.process[temId].abortController;
  };
  const processStart = isOutDrag ? dragProcessStart : normalProcessStart;
  const onprocess = ({ uploaded, total, temId }:{uploaded:number, total:number, temId:string}) => {
    const percent = Math.max(0, Math.min(Math.round(uploaded / total * 100), 100));
    if (state.process[temId]) {
      state.process[temId].percent = percent;
    }
  };
  const getTemSkuPosition = (temId:string) => {
    let position = { colI: -1, itemI: -1 };
    (_this.currentCols as Col[]).forEach((_col, colI) => {
      (_col.itemInfos as Item[]).forEach((item, itemI) => {
        if (item.id === temId) {
          position = { colI, itemI };
        }
      });
    });
    return position;
  };
  const uploadDone = (sku:Item, temId:string) => {
    const { colI, itemI } = getTemSkuPosition(temId);
    _this.$set(_this.currentCols[colI].itemInfos, itemI, sku);
    _this.$delete(state.process, temId);
  };
  const cancelUpload = (item:Item, process:Process) => {
    process[item.id].abortController.abort();
    _this.$delete(process, item.id);
    if (item.isTempSku) {
      // item
      const { colI, itemI } = getTemSkuPosition(item.id);
      _this.currentCols[colI].itemInfos.splice(itemI);
    }
  };
  return {
    ...toRefs(state),
    processStart,
    dragProcessStart,
    normalProcessStart,
    onprocess,
    uploadDone,
    cancelUpload,
  };
};

export default useProcess;

